<template>
  <div class="container">
    <Title-item title="留存数据">
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-date-picker
        class="mgl16"
        v-model="time"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-button style="margin-left: 16px" @click="search" type="primary">搜 索</el-button>
    </Title-item>
    <el-table class="mgt24" :data="dataList" :header-cell-style="{ background:'#F2F3F7',color:'#606266'}">
      <el-table-column prop="time" label="日期"></el-table-column>
      <el-table-column prop="day0" label="新增用户数">
        <template slot-scope="scope">
          {{scope.row.day0 | placeholder(value, 'day0')}}
        </template>
      </el-table-column>
      <el-table-column prop="day1" label="1天后">
        <template slot-scope="scope">
          {{scope.row.day1 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day2" label="2天后">
        <template slot-scope="scope">
          {{scope.row.day2 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day3" label="3天后">
        <template slot-scope="scope">
          {{scope.row.day3 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day4" label="4天后">
        <template slot-scope="scope">
          {{scope.row.day4 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day5" label="5天后">
        <template slot-scope="scope">
          {{scope.row.day5 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day6" label="6天后">
        <template slot-scope="scope">
          {{scope.row.day6 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day7" label="7天后">
        <template slot-scope="scope">
          {{scope.row.day7 | placeholder(value)}}
        </template>
      </el-table-column>
      <el-table-column prop="day30" label="30天后">
        <template slot-scope="scope">
          {{scope.row.day30 | placeholder(value)}}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import TitleItem from '../components/TitleItem'
import { getRetainedData } from '@/api/services/dataProfile'
import { toFixed } from '@/utils'
import moment from 'moment'

let DATE = new Date(), beforeTime, afterTime
beforeTime = (day, start = DATE) => start - 86400 * day * 1000 // day 获取多少天之前的
afterTime = (day, start = DATE) => start + 86400 * day * 1000 // day 获取多少天之后的

export default {
  name: 'index',
  components: {
    TitleItem
  },
  filters: {
    placeholder(data, state, day) {
      if (state) return data || 0
      const unit = day ? '' : '%'
      return (data || 0) + unit
    }
  },
  computed: {
    dataList() {
      return this.tableDataList.map((v, i) => {
        const time = moment(v.time).format('YYYYMMDD') - 0
        let data = {
          time: moment(v.time).format('YYYY-MM-DD')
        }
        this.tableData.forEach(e => {
          if (time === e.Ymd && e.byDay === 0) data.day0 = e.activeUserNum
        })
        this.tableData.forEach(e => {
          let afterDate = moment(afterTime(e.byDay, v.time)).format('YYYYMMDD') - 0
          if (afterDate === e.Ymd) {
            if (this.value) {
              // 留存数
              data['day' + e.byDay] = e.activeUserNum
            } else {
              // 留存率
              if (e.byDay !== 0) {
                data['day' + e.byDay] = toFixed(e.activeUserNum / data.day0)
              }
            }
          }
        })
        return data
      })
    }
  },
  data() {
    return {
      tableDataList: [],
      time: [moment(beforeTime(10)), DATE],
      total: 0,
      value: 1,
      params: {
        startAt: moment(beforeTime(10)).format('YYYYMMDD'),
        endAt: moment(DATE).format('YYYYMMDD')
      },
      options: [
        {
          label: '留存数',
          value: 1
        },
        {
          label: '留存率',
          value: 0
        }
      ],
      tableData: []
    }
  },
  created() {
    this.getTimeList(11)
    this.getDataList()
  },
  methods: {
    getTimeList(day, start) {
      let dataList = []
      for (let i = 0; i < day; i++) {
        dataList.push({ time: beforeTime(i, start) })
      }
      this.tableDataList = dataList.reverse()
    },
    search() {
      if (Array.isArray(this.time) && this.time.length >= 2) {
        const day = (this.time[1] - this.time[0]) / (1000 * 60 * 60 * 24)
        this.getTimeList(day + 1, this.time[1] - 0)
      }
      this.getDataList()
    },
    getDataList() {
      const date = Array.isArray(this.time) && this.time.length >= 2 ? this.moment(this.time) : this.params
      getRetainedData(date).then(res => {
        if (!res) return
        this.tableData = res.list
        // this.tableData = [
        //     {"activeUserNum": 500, "Ymd": 20190724, "byDay": 0},
        //     {"activeUserNum": 500, "Ymd": 20190723, "byDay": 0},
        //     {"activeUserNum": 400, "Ymd": 201907229, "byDay": 4},
        //     {"activeUserNum": 500, "Ymd": 20190723, "byDay": 0},
        //     {"activeUserNum": 500, "Ymd": 20190723, "byDay": 0},
        //     {"activeUserNum": 10, "Ymd": 20190728, "byDay": 4},
        // ];
      })
    },
    moment(time) {
      return {
        startAt: moment(time[0]).format('YYYYMMDD'),
        endAt: moment(time[1]).format('YYYYMMDD')
      }
    }
  }
}
</script>

<style scoped lang="less">
  .container {
    background: #fff;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 24px 16px;
  }
</style>
