import request from '../request'
import { formatGetParams } from '@/utils'
import config from '@/config/urls'

// 获取流失数据
export function getLostCustomerApi(data) {
  return request({
    url: '/basic/overview/lost-customer-data',
    params: data
  })
}

// 获取活跃数据
export function getActiveCustomerApi(data) {
  return request({
    url: '/basic/overview/active-customer-data',
    params: data
  })
}

// 获取现金资产数据
export function getAssetDataApi(data) {
  return request({
    url: '/basic/overview/property-data',
    params: data
  })
}

// 获取总资产数据
export function getOverviewDataApi(data) {
  return request({
    url: '/basic/overview/overview-data',
    params: data
  })
}

/**
 * 聚一数据概况
 * */
export function getIndicators(params) {
  return request({
    url: `basic/overview/data-indicators`,
    params
  })
}

export function getOrderData(params) {
  return request({
    url: `basic/overview/order-data`,
    params
  })
}

export function getGoodsData(params) {
  return request({
    url: `basic/overview/goods-data`,
    params
  })
}

export function getUserData(params) {
  return request({
    url: `basic/overview/user-data`,
    params
  })
}

export function getRetainedData(params) {
  return request({
    url: `basic/overview/retained-data`,
    params
  })
}

export function getOrderDetail(params) {
  return request({
    url: `basic/overview/hsh-mall-order-detail-overview`,
    method: 'post',
    data: params
  })
}

export function hshDataOverview(params) {
  return request({
    url: `basic/overview/hsh-data-overview?${formatGetParams(params)}`,
    method: 'get',
    data: params
  })
}

// 商品数据 导出
export function exportGoodsData(params) {
  params.t = sessionStorage.getItem('t')

  params = formatGetParams(params)

  window.open(`${config.backend}/basic/overview/export-goods-data?${params}`)
}

// 商品数据 导出
export function exportOrderDetail(params) {
  params.t = sessionStorage.getItem('t')

  params = formatGetParams(params)

  window.open(`${config.backend}/basic/overview/hsh-mall-order-detail-overview?${params}`)
}
