<template>
  <div class="conversion_item">
    <p class="blue">{{title}}</p>
    <div class="conversion_total">
      <el-progress type="circle" :percentage="percentage"></el-progress>
      <div class="conversion_total_item">
        <div>
          访客人数
          <p>{{TotalData.goodsDetailNumber || 0}}</p>
        </div>
        <div style="margin-top: 20px;">
          兑换件数
          <p>{{TotalData.totalNum || 0}}</p>
        </div>
      </div>
    </div>
    <el-table
      :data="dataList"
      style="width: 100%; margin-top: 16px;">
      <el-table-column
        prop="name"
        label="商品"
        width="300"
      >
      </el-table-column>
      <el-table-column
        prop="visitorNumber"
        label="访问数"
      >
      </el-table-column>
      <el-table-column
        prop="totalNum"
        label="兑换数">
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
import { toFixed } from '@/utils'

export default {
  name: 'Conversion',
  props: ['TotalData', 'dataList', 'title'],
  data() {
    return {}
  },
  computed: {
    percentage() {
      if (this.TotalData.goodsDetailNumber - 0 === 0) return 0
      let total = (this.TotalData.totalNum / this.TotalData.goodsDetailNumber) || 0, result = toFixed(total, 2) * 100
      return toFixed(result) > 100 ? 100 : toFixed(result)
    }
  }
}
</script>

<style lang='less' scoped>
  .conversion_item {
    flex: 1;
    text-align: center;
    padding-bottom: 24px;
    color: #000;
  }

  .blue {
    color: cornflowerblue;
  }

  .conversion_total {
    display: flex;
    justify-content: center;
    margin-top: 50px;

    .conversion_total_item {
      margin-left: 20px;

      > div {
        > p {
          font-size: 24px;
          line-height: 1.6em;
          color: cornflowerblue;
        }
      }
    }
  }
</style>
