<template>
  <div class="profile-data" v-loading="loading">
    <div class="group grown-data">
      <div class="group-head">
        <div class="group-title">
          <h1>资产数据</h1>
          <span class="tips">(当天默认显示昨天数据)</span>
        </div>

        <div class="screen">
          <el-date-picker
            v-model="time"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :clearable="false"
            :picker-options="pickerOptions">
          </el-date-picker>

          <el-button type="primary" @click="getAssetData">查询</el-button>
        </div>
      </div>

      <div class="group-content">
        <div class="grown-data-list">
          <div class="grown-data-item">
            <div class="title">
              现金资产数据

              <el-select v-model="appletId" placeholder="请选择" clearable @change="getAssetData">
                <el-option
                  v-for="(item, key) in appletList"
                  :key="item"
                  :label="item"
                  :value="key">
                </el-option>
              </el-select>
            </div>
            <totalData :data="cashAssetsData"></totalData>
          </div>
        </div>

        <div class="grown-data-list point-asset-data">
          <div class="grown-data-item">
            <div class="title">积分资产数据</div>

            <div class="point-asset-data-list">
              <totalData :data="pointsDistributionData" mode="horizontal"></totalData>
              <totalData :data="pointsRecoveryData" mode="horizontal"></totalData>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import totalData from './totalData'
import dateOptionsMixins from '@/components/dateOptionsMixins'
import { formatTime } from '@/utils/index'
import { getAssetDataApi } from '@/api/services/dataProfile'

export default {
  name: 'profileData',
  components: {
    totalData
  },
  mixins: [dateOptionsMixins],
  data() {
    const nowDate = Date.now(),
      oneDay = 86400 * 1000

    return {
      loading: false, // 加载loading
      time: [new Date(nowDate - oneDay), new Date(nowDate - oneDay)], // 日期筛选
      appletId: '', // 广告类型筛选

      appletList: {},

      // 现金资产数据
      cashAssetsData: [
        {
          name: '商家联盟GMV',
          value: 0,
          key: 'gmv',
          prefix: '¥'
        },
        {
          name: '现金购买商品金额',
          value: 0,
          key: 'cash',
          prefix: '¥'
        }
      ],
      // 积分发放数据
      pointsDistributionData: [
        {
          name: '积分发放总额',
          value: 0,
          link: 'IntegralAsset',
          key: 'expenditure',
          prefix: '¥',
          params: {
            activeName: 'Assets'
          }
        },
        {
          name: '积分发放总资产',
          value: 0,
          link: 'IntegralAsset',
          key: 'expenditureAdf',
          prefix: 'ADF',
          params: {
            activeName: 'Assets'
          }
        }
      ],
      // 积分回收数据数据
      pointsRecoveryData: [
        {
          name: '积分回收消耗总额',
          value: 0,
          link: 'IntegralAsset',
          key: 'income',
          prefix: '¥',
          params: {
            activeName: 'Assets'
          }
        },
        {
          name: '积分回收消耗总资产',
          value: 0,
          link: 'IntegralAsset',
          key: 'incomeAdf',
          prefix: 'ADF',
          params: {
            activeName: 'Assets'
          }
        }
      ]
    }
  },
  created() {
    this.getAssetData()
  },
  methods: {
    // 获取资产数据
    getAssetData() {
      let _this = this,
        time = _this.time,
        params = {
          appletId: _this.appletId || undefined
        }

      if (time) {
        params.startAt = formatTime(time[0], 'yyyyMMdd')
        params.endAt = formatTime(time[1], 'yyyyMMdd')
      }

      _this.loading = true

      getAssetDataApi(params).then(res => {
        _this.appletList = res.appletList
        _this.handlerAssetData(res.data)

        _this.loading = false
      })
    },

    // 处理资产数据
    handlerAssetData(data) {
      let _this = this,
        gmv = Number(data.gmv || 0),
        cash = Number(data.cash || 0),
        handlerDataList = [_this.cashAssetsData, _this.pointsDistributionData, _this.pointsRecoveryData]

      data.gmv = gmv / 100
      data.cash = cash / 100

      handlerDataList.forEach(item => {
        item.forEach(childItem => {
          let currentValue = data[childItem.key] || 0

          childItem.value = currentValue
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.profile-data {
  .group {
    border-radius: 8px;
    margin: 0 16px;

    & + .group {
      margin-top: 16px;
    }

    .group-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 24px 16px;
      background: #fff;
      border-radius: 8px;
      margin-bottom: 16px;

      .group-title {
        display: flex;
        align-items: flex-end;
        color: #333;

        h1 {
          margin-right: 8px;
          font-size: 20px;
        }

        .tips {
          font-size: 12px;
        }
      }

      .screen {
        .el-button {
          margin-left: 16px;
        }
      }
    }

    &.grown-data {
      .grown-data-list {
        display: flex;

        & + .grown-data-list {
          margin-top: 16px;
        }

        &.point-asset-data {
          .point-asset-data-list {
            display: flex;

            .total-data {
              flex: 1;
            }
          }
        }

        .grown-data-item {
          width: 100%;
          background: #fff;
          border-radius: 8px;

          .title {
            padding: 24px 16px;
            color: #333;
            font-size: 16px;
          }
        }
      }
    }
  }
}
</style>
