<template>
  <div>
    <div class="container">
      <Total-data :totalList="totalList" class="mgt16"></Total-data>
    </div>
    <el-row>
      <el-col :lg="12" :sm="24">
        <div id="loop"></div>
      </el-col>
      <el-col :lg="12" :sm="24" class="pdl16">
        <div id="loop1"></div>
      </el-col>
      <el-col :lg="12" :sm="24">
        <div id="cake"></div>
      </el-col>
      <el-col :lg="12" :sm="24" class="pdl16">
        <div id="cake1"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import TotalData from '../components/TotalData'
import echarts from 'echarts'
import { getUserData } from '@/api/services/dataProfile'

export default {
  name: 'index',
  components: {
    TotalData
  },
  data() {
    return {
      totalList: [
        {
          label: '注册用户数',
          key: 'channelData',
          value: 0
        },
        {
          label: '用户消费次数',
          key: 'consumeData',
          value: 0
        }
      ]
    }
  },
  methods: {
    cakeEcharts(data, name, dom) {
      let cake = echarts.init(document.getElementById(dom)), cakeData = cakeOption()
      cakeData.title.text = name
      cakeData.series[0].name = name
      cakeData.series[0].data = data
      cake.setOption(cakeData)
    },
    loopEcharts(data, name, dom) {
      let loop = echarts.init(document.getElementById(dom)), loopData = loopOption()
      loopData.title.text = name
      loopData.series[0].data = data
      loopData.series[0].name = name
      loop.setOption(loopData)
    }
  },
  created() {
    getUserData().then((res) => {
      let { channelData, consumeData, fillFormData, sexData } = res
      const [{ inviteNumber, QRCode, naturalRegistered }, { exchangeCouponCount, giveCouponCount, buyGoodsCount, exchangeGoodsCount },
        { notFillFormUserNumber, fillFormUserNumber }, { maleNumber, femaleNumber }] = [channelData, consumeData, fillFormData, sexData]
      this.totalList.forEach(v => {
        v.value = res[v.key].total
      })
      // 用户男女比例数据
      this.cakeEcharts([
        { value: maleNumber, name: '男性' },
        { value: femaleNumber, name: '女性' }
      ], '用户男女比例数据', 'cake')
      // 用户渠道来源数据
      this.loopEcharts([
        { value: inviteNumber, name: '游戏邀请' },
        { value: QRCode, name: '大屏扫码' },
        { value: naturalRegistered, name: '自然注册' }
      ], '用户渠道来源数据', 'loop')
      // 填写表单用户数据
      this.cakeEcharts([
        { value: fillFormUserNumber, name: '填写表单用户' },
        { value: notFillFormUserNumber, name: '未填写表单用户' }
      ], '填写表单用户数据', 'cake1')
      // 用户消费数据
      this.loopEcharts([
        { value: exchangeCouponCount, name: '兑换点播劵' },
        { value: giveCouponCount, name: '赠送点播劵' },
        { value: buyGoodsCount, name: '购买商品' },
        { value: exchangeGoodsCount, name: '兑换商品' }
      ], '用户消费数据', 'loop1')
    })
  }
}
let colorList = [
  '#6699FF', '#836DF2', '#CE61F2', '#FF809A', '#FF9F40'
]

function cakeOption() {
  return {
    title: {
      text: '用户男女比例数据',
      x: 'left'
    },
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
      orient: 'vertical',
      left: 'right'
    },
    color: function(params) {
      return colorList[params.dataIndex]
    },
    series: [
      {
        type: 'pie',
        radius: '65%',
        center: ['50%', '50%'],
        label: {
          formatter: '{b} : {c} ({d}%)'
        },
        data: [],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  }
}

function loopOption() {
  return {
    title: {
      x: 'left'
    },
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    legend: {
      orient: 'vertical',
      x: 'right'
    },
    color: function(params) {
      return colorList[params.dataIndex]
    },
    series: [
      {
        type: 'pie',
        radius: ['50%', '70%'],
        avoidLabelOverlap: true,
        label: {
          formatter: '{b} : {c} ({d}%)'
        },
        labelLine: {
          normal: {
            show: true
          }
        },
        data: []
      }
    ]
  }
}
</script>

<style lang='less' scoped>
  #loop, #loop1, #cake, #cake1 {
    height: 240px;
    background: #fff;
    border-radius: 8px;
    padding: 16px;
  }

  .el-col {
    margin-bottom: 16px;
    border-radius: 8px;
  }

  @media only screen and (min-width: 1200px) {
    .pdl16 {
      padding-left: 16px;
    }
  }

  .container {
    background: #fff;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 24px 16px;

    > .mgt16 {
      margin-top: 16px;
    }
  }
</style>
