import { getEnv } from '../utils/index'

let medialibs, // 素材库
  admin, // admin账号
  backend, // 总后台
  henshihuiMall // 很实惠商城

if (/^localhost/.test(location.host)) {
  medialibs = 'http://medialibs-dev.idouzi.com'
  backend = 'http://data-manage-backend-dev.henshihui.com'
  henshihuiMall = 'http://mall-dev.henshihui.com'
  admin = 'http://admin-dev.idouzi.com'
} else {
  switch (getEnv()) {
  case 'dev':
    medialibs = 'http://medialibs-dev.idouzi.com'
    backend = 'http://data-manage-backend-dev.henshihui.com'
    henshihuiMall = 'http://mall-dev.henshihui.com'
    admin = 'http://admin-dev.idouzi.com'

    break
  case 'test':
    medialibs = 'http://medialibs-test.idouzi.com'
    backend = 'http://data-manage-backend-test.henshihui.com'
    henshihuiMall = 'http://mall-test.henshihui.com'
    admin = 'http://admin-test.idouzi.com'

    break
  case 'prod':
    medialibs = 'http://medialibs.idouzi.com'
    backend = 'http://data-manage-backend.henshihui.com'
    henshihuiMall = 'http://mall.henshihui.com'
    admin = 'http://admin.idouzi.com'

    break

  default:
    medialibs = 'http://medialibs.idouzi.com'
    backend = 'http://data-manage-backend.henshihui.com'
    henshihuiMall = 'http://mall.henshihui.com'
    admin = 'http://admin.idouzi.com'

    break
  }
}

export default {
  medialibs,
  admin,
  backend,
  henshihuiMall
}
