<template>
  <div class="plaform-active-data-wrap">
    <el-tabs class="admin-header"
      v-model="activeTab">
      <el-tab-pane class="tab-item" label="用户活跃数据" name="platformActiveData"></el-tab-pane>
      <el-tab-pane class="tab-item" label="积分用户活跃数据" name="platformDigitalActiveData"></el-tab-pane>
    </el-tabs>
    <div class="section-wrap">
      <div class="ops-wrap">
        <el-select @change="platformSelect"
          v-model="platformTypeValue"
          class="select-wrap"
          placeholder="请选择平台">
          <el-option value="" label="全部平台"></el-option>
          <el-option
            v-for="(item, index) in platformList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-select @change="timeTypeSelect"
          v-model="timeTypeValue"
          class="select-wrap"
          placeholder="最近使用时间">
          <el-option
            v-for="item in timeTypeList"
            :key="item.name"
            :value="item.value"
            :label="item.name">
          </el-option>
        </el-select>
        <el-date-picker
          class="picker-wrap"
          v-model="timeListValue"
          type="daterange"
          :clearable="false"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          :picker-options="pickerOptions"
          value-format="yyyyMMdd"
          @change="timeDataSelect">
        </el-date-picker>
        <el-input class="input-wrap"
          placeholder="请输入用户手机号、昵称进行搜索"
          prefix-icon="el-icon-search"
          v-model="inputSearchValue">
        </el-input>

        <el-button class="btn" type="primary" @click="getListData">查询</el-button>
        <el-button class="btn" type="primary" @click="exportData">导出</el-button>
      </div>
      <div class="user-num-wrap">
        <h2 class="num-title">活跃积分用户数</h2>
        <p>{{pointEffectiveUserTotal}}</p>
      </div>
    </div>

    <div class="section-wrap">
      <h1 class="title">用户列表(当天默认显示昨日数据)</h1>
      <el-table class="table-wrap"
        header-row-class-name="table-header"
       :data="tableDataList">
        <el-table-column
          align="center"
          prop="employTime"
          label="最近使用时间">
        </el-table-column>
        <el-table-column
          align="center"
          prop="unionId"
          label="UnionID">
        </el-table-column>
        <el-table-column
          align="center"
          prop="nickName"
          label="用户昵称">
        </el-table-column>
        <el-table-column
          align="center"
          prop="phone"
          label="手机号码">
        </el-table-column>
        <el-table-column
          align="center"
          label="平台">
          <template slot-scope="scope">
            {{platformList[scope.row.appletId]}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="inIntegral"
          label="获得积分">
        </el-table-column>
        <el-table-column
          align="center"
          prop="outIntegral"
          label="消耗积分">
        </el-table-column>
        <el-table-column
          align="center"
          prop="balance"
          label="积分余额">
        </el-table-column>
        <el-table-column
          align="center"
          label="类型">
          <template slot-scope="scope">
            {{scope.row.pointType == 1 ? '金豆' : scope.row.pointType == 2 ? '米粒' : scope.row.pointType}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="操作">
          <template slot-scope="scope">
            <el-button @click="showUserDetail(scope.row)">查看明细</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="(totalCount / pageSize)<= 1"
        :page-size="pageSize"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPlatformDigitalActiveData, exportDataDefault } from '@/api/services/platformUserManage'
import { formatTime } from '@/utils'

export default {
  name: 'platform-active-data',
  components: {},
  data() {
    return {
      activeTab: 'platformDigitalActiveData', // 当前切换真的
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      platformTypeValue: '', // 选择的平台类型，默认为空，表示全部
      platformList: {
        '1': '很实惠',
        '2': '聚驿优品'
      },
      timeTypeValue: 'employTime', // 选择的用户来源类型，默认为空，表示全部
      timeTypeList: [
        {
          name: '最近使用时间',
          value: 'employTime'
        },
        {
          name: '统计时间',
          value: ''
        }
      ],
      timeListValue: [], // 选择的时间段
      inputSearchValue: '',
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      pointEffectiveUserTotal: 0, // 积分活跃用户总人数
      tableDataList: [
        // {
        //   nickName: 'test002',
        //   unionId: 'test000001001000test2',
        //   appletId: '2',
        //   source: 1,
        //   regTime: 1563696886
        // }
      ] // 数据列表数据来源
    }
  },
  created() {
    let _this = this,
      // end = new Date(), // 设置日期为当前日期
      start = new Date(new Date().setDate(new Date().getDate() - 1)), // 设置日期为当前日期前七天
      formattedTime = formatTime(start, 'yyyyMMdd')

    _this.timeListValue = [formattedTime, formattedTime]
    _this.getListData()
  },

  watch: {
    activeTab(newVal) {
      this.$router.push({
        name: newVal
      })
    }
  },

  methods: {

    getListData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          timeFilter: _this.timeTypeValue,
          searchWord: _this.inputSearchValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getPlatformDigitalActiveData(params).then(res => {
          _this.tableDataList = res.list
          _this.pointEffectiveUserTotal = res.pointEffectiveUserTotal
          _this.pageSize = res.pageSize
          _this.totalCount = res.totalCount
          _this.isPost = false
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          timeFilter: _this.timeTypeValue,
          searchWord: _this.inputSearchValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page,
          isExport: 1
        }

      exportDataDefault('platformDigitalActiveData', params)
    },

    platformSelect() {
      this.getListData()
    },

    timeTypeSelect() {
      this.getListData()
    },

    handleSizeChange(size) {
      this.pageSize = size
    },

    handleCurrentChange(page) {
      let _this = this

      _this.page = page - 1
      _this.getListData()
    },

    timeDataSelect(time) {
      let _this = this

      _this.timeListValue = time
      _this.getListData()
    },

    showUserDetail(item) {
      let _this = this

      _this.$router.push({
        name: 'platformUserDigitalDetail',
        query: {
          unionId: item.unionId,
          username: item.nickName
        }
      })
    }
  }
}
</script>

<style lang="less">
.plaform-active-data-wrap {
  .el-tabs__item {
    height: 56px;
    line-height: 56px;
    font-size: 16px;
    color: #333;
  }

  .admin-header {
    .el-tabs__header {
      margin: 0;
    }
  }
}
</style>

<style lang="less" scoped>
.plaform-active-data-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .admin-header {
    padding: 0 32px;
    margin-bottom: 16px;
    background: #fff;
  }

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;
  }

  .title {
    padding-bottom: 12px;
    font-size: 20px;
    line-height: 28px;
    border-bottom: 1px solid #F2F3F7;
  }

  .ops-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }

  .select-wrap {
    margin-bottom: 12px;
    width: 144px;
    margin-right: 16px;
  }

  .picker-wrap {
    margin-bottom: 12px;
    width: 300px;
    margin-right: 16px;
  }

  .input-wrap {
    margin-bottom: 12px;
    width: 240px;
    margin-right: 16px;
  }

  .btn {
    margin-bottom: 12px;
  }

  .user-num-wrap {
    margin: 8px auto 0px auto;
    max-width: 300px;
    padding: 24px 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .num-title {
      font-size: 14px;
      color: #606166;
    }

    p {
      padding-top: 12px;
      font-size: 40px;
      color: #4D5AFF;
    }
  }

  .pagination-wrap {
    margin-top: 16px;
    text-align: right;
  }
}
</style>
