<template>
  <div>
    <div class="container">
      <Title-item title="订单数据" type="search" @handleData="search"></Title-item>
      <div class="order_container">
        <div class="data_content_left">
          <div v-for="(item, index) in orderData" :key="index">
            <p class="total_item_label">{{item.label}}</p>
            <p class="total_item_val">{{item.value}}</p>
          </div>
        </div>
        <!--漏斗-->
        <div class="main_box">
          <Funnel :dataList="funnelList" title="订单转化漏斗"></Funnel>
        </div>
      </div>
    </div>
    <!--订单明细数据-->
    <div class="container">
      <Title-item title="订单明细数据" type="export" @handleData="exportData"></Title-item>
      <el-table class="mgt24" @sort-change="sortChange" :data="tableData"
                :header-cell-style="{ background:'#F2F3F7',color:'#606266'}">
        <el-table-column prop="date" label="日期"></el-table-column>
        <el-table-column prop="orderUnionIdNum" label="下单用户数"></el-table-column>
        <el-table-column prop="orderNum" label="下单数量"></el-table-column>
        <el-table-column prop="orderPayedNum" label="支付订单数"></el-table-column>
        <el-table-column prop="orderConversionRate" label="订单转化率">
        </el-table-column>
        <el-table-column prop="orderPayedMoney" label="支付金额" sortable>
          <template slot-scope="scope">
            {{scope.row.orderPayedMoney | price}}
          </template>
        </el-table-column>
        <el-table-column prop="orderPayedPoints" label="回收积分数"></el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="20"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getOrderData, getOrderDetail, exportOrderDetail } from '@/api/services/dataProfile'
import Funnel from '../components/Funnel'
import TitleItem from '../components/TitleItem'
import { toFixed } from '@/utils'
import moment from 'moment'

const DATE = new Date()
let beforeTime, DATE_STR
beforeTime = day => DATE - 86400 * day * 1000 // day 获取多少天之前的
DATE_STR = moment(beforeTime(1)).format('YYYYMMDD') // 昨天

export default {
  name: 'index',
  components: {
    Funnel,
    TitleItem
  },
  data() {
    return {
      funnelList: [],
      total: 0,
      orderData: [
        {
          label: '下单用户数',
          key: 'orderUserNumber',
          value: 0
        },
        {
          label: '支付订单数',
          key: 'payUserNumber',
          value: 0
        },
        {
          label: '订单转化率',
          key: 'convRate',
          value: '0%'
        }
      ],
      params: {
        page: 1,
        startAt: DATE_STR,
        endAt: DATE_STR
      },
      tableData: []
    }
  },
  methods: {
    sortChange(v) {
      let sortRules = {
        sortField: v.prop,
        sortOrder: v.order === 'ascending' ? 'ASC' : 'DESC'
      }
      Object.assign(this.params, sortRules)
      this.getOrderDetail()
    },
    exportData() {
      exportOrderDetail({ ...this.params, isExport: 1 })
    },
    search(params) {
      const param = Object.assign(this.params, params)
      this.getOrderData(param)
      this.getOrderDetail()
    },
    funnel(data) {
      const per = val => {
        if (!val) return 0
        return toFixed(val * 100)
      }
      let test, compare
      test = [
        {
          name: '首页访问PV',
          value: data.homePagePv || 0,
          per: per(data.convRateHomePagePv)
        },
        {
          name: '商品详情页访问PV',
          value: data.goodsDetailPagePv || 0,
          per: per(data.convRateGoodsDetailPagePv)
        },
        {
          name: '下单用户数',
          value: data.orderUserNumber || 0,
          per: per(data.convRateOrderUser)
        },
        {
          name: '支付用户数',
          value: data.payUserNumber || 0,
          per: per(data.convRatePayUser)
        }
      ]
      compare = (obj1, obj2) => {
        const [val1, val2] = [Number(obj1.value), Number(obj2.value)]
        if (val1 < val2) {
          return 1
        } else if (val1 > val2) {
          return -1
        } else {
          return 0
        }
      }
      this.funnelList = test.sort(compare)
    },
    handleCurrentChange(val) {
      this.params.page = val
      this.getOrderDetail()
    },
    getOrderData(params) {
      getOrderData(params).then(res => {
        if (!res) return
        const orderData = {
          ...res,
          convRate: `${toFixed(res.convRate * 100)}%`
        }
        this.funnel(res)
        this.orderData.forEach(v => {
          v.value = orderData[v.key]
        })
      })
    },
    getOrderDetail() {
      getOrderDetail(this.params).then(res => {
        if (!res) return
        this.tableData = res.list
        this.total = res.totalCount
      })
    }
  },
  created() {
    this.getOrderData(this.params)
    this.getOrderDetail()
  },
  filters: {
    price(data) {
      return toFixed(data / 100)
    }
  }
}
</script>

<style scoped lang="less">

  .container {
    background: #fff;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 24px 16px;

    .order_container {
      display: flex;
      margin-top: 40px;

      .main_box {
        flex: 1;
      }
    }

    .data_content_left {
      display: flex;
      width: 260px;
      flex-direction: column;
      justify-content: center;
      margin-top: 48px;

      > div {
        text-align: center;
        margin-bottom: 46px;

        .total_item_label {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #606166;
        }

        .total_item_val {
          font-family: PingFangSC-Regular;
          font-size: 40px;
          color: #4D5AFF;
          margin-top: 10px;
        }
      }

      > div:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  .mgt24 {
    margin-top: 24px;
  }
</style>
