<template>
  <div class="funnel_container">
    <h3>{{title}}</h3>
    <div :class="'funnel_item'+index" v-for="(item, index) in dataList" :key="index">
      <span>{{item.name}}</span>
      <div class="funnel_data">
        <p class="funnel_data_number">{{item.value}}</p>
        <p>环比增长</p>
        <div class="funnel_data_per">
          <span>{{item.per | abs}}%</span>
          <i :class="className(item.per)"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Funnel',
  props: ['dataList', 'title'],
  data() {
    return {}
  },
  filters: {
    abs(data) {
      return data < 0 ? Math.abs(data) : data
    }
  },
  methods: {
    className(val) {
      return (val / 100 >= 0) ? 'el-icon-top' : 'el-icon-bottom down'
    }
  }
}
</script>

<style lang='less' scoped>
  .funnel_container {
    display: flex;
    align-items: center;
    flex-direction: column;

    > h3 {
      font-weight: normal;
      margin-bottom: 24px;
      color: #000;
      font-size: 18px;
    }

    > div {
      position: relative;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;

      > span {
        position: absolute;
        color: #fff;
        font-size: 16px;
      }

      > .funnel_data {
        position: absolute;
        width: 200px;
        height: 100%;
        right: -240px;

        > .funnel_data_number {
          font-size: 24px;
          line-height: 1.6em;
        }

        > .funnel_data_per {
          font-size: 14px;

          > i {
            font-size: 20px;
            color: #45B07F;
            vertical-align: middle;
          }

          > .down {
            color: red;
          }
        }
      }
    }

    > div::before {
      height: 80px;
      content: '';
      transform: perspective(30px) rotateX(-2deg);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .funnel_item0 {
      width: 450px;

      &::before {
        background: #55B6FF;
      }
    }

    .funnel_item1 {
      width: 374px;

      &::before {
        background: #45B07F;
      }
    }

    .funnel_item2 {
      width: 310px;

      &::before {
        background: #F38E3B;
      }
    }

    .funnel_item3 {
      width: 240px;

      &::before {
        background: #DC5B57;
      }
    }
  }

  p, span {
    color: #000;
  }
</style>
