<script>
const obj = {},
  sortArr = []
export default {
  props: {
    tableData: {
      type: Array,
      default() {
        return []
      }
    },
    totalCount: {
      type: Number,
      default() {
        return 0
      }
    },
    tableConfig: {
      type: Array,
      default() {
        return []
      }
    }
  },
  created() {},
  // 根据配置渲染出表格
  render() {
    return (
      <div>
        <el-table data={this.tableData} stripe on-sort-change={this.sortChange}>
          {this.tableConfig.map(item => {
            if (item.formatter) {
              return (
                <el-table-column
                  sortable={item.sortable || false}
                  prop={item.prop}
                  label={item.label}
                  key={item.prop}
                  formatter={item.formatter}
                />
              )
            }
            return (
              <el-table-column
                sortable={item.sortable || false}
                prop={item.prop}
                label={item.label}
                key={item.prop}
              />
            )
          })}
        </el-table>
        <el-pagination
          on-current-change={this.onChange}
          background
          layout="prev, pager, next"
          total={this.totalCount}
          style="margin-top: 20px;float: right"></el-pagination>
      </div>
    )
  },
  methods: {
    // 监听排序变动并派发事件
    sortChange({ column, prop, order }) {
      const o = {
          name: prop,
          sort: order == 'ascending' ? 'ASC' : 'DESC'
        },
        index = sortArr.findIndex(item => item.name === o.name)
      if (index > -1) {
        sortArr[index].sort = o.sort
      } else {
        sortArr.push(o)
      }
      obj['sortArr'] = JSON.stringify(sortArr)
      this.$emit('change', obj)
    },
    // 监听分页变动并派发事件
    onChange(page) {
      obj.page = page
      this.$emit('change', obj)
    }
  }
}
</script>

<style scoped lang="less"></style>
