import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'

// 数据概况
import dataProfile from './views/dataProfile/route'
// 平台用户管理分路由
import platformUserManageRouter from './views/platformUserManage/route'
// 积分活动管理分路由
import integralActiveManageRouter from './views/integralActiveManage/route'
// import aiduifen from ''
import aiduifenRouter from './views/aiduifen/route'
import assetsManage from './views/assetsManage/route'
// 积分商品
import pointsMallManagement from './views/pointsMallManagement/route'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        dataProfile,
        aiduifenRouter,
        assetsManage, // 资产管理
        platformUserManageRouter, // 平台用户管理分路由
        integralActiveManageRouter, // 积分活动管理分路由
        pointsMallManagement
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
    // 按需加载demo
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})
