<template>
  <div class="profile-data" v-loading="loading">
    <div class="group grown-data">
      <div class="group-head">
        <div class="group-title">
          <h1>增长指标</h1>
          <span class="tips">(当天默认显示昨天数据)</span>
        </div>

        <div class="screen">
          <el-date-picker
            v-model="time"
            :clearable="false"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions">
          </el-date-picker>

          <el-button type="primary" @click="getOverviewData">查询</el-button>
        </div>
      </div>

      <div class="group-content">
        <div class="grown-data-list">
          <el-row :gutter="16" style="width: 100%">
            <el-col :span="12">
              <div class="grown-data-item">
                <div class="title">用户增长数据</div>
                <totalData :data="userGrowthData"></totalData>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="grown-data-item">
                <div class="title">爱兑分用户增长数据</div>
                <totalData :data="walletUserGrowthData"></totalData>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="grown-data-list">
          <div class="grown-data-item">
            <div class="title">积分用户增长数据</div>
            <totalData :data="pointsUserGrowthData"></totalData>
          </div>
        </div>
      </div>
    </div>

    <div class="group ad-data">
      <div class="group-head">
        <div class="group-title">
          <h1>广告数据</h1>
          <span class="tips">(当天默认显示昨天数据)</span>
        </div>
      </div>

      <div class="group-content">
        <totalData :data="adData"></totalData>
      </div>
    </div>
  </div>
</template>

<script>
import totalData from './totalData'
import dateOptionsMixins from '@/components/dateOptionsMixins'
import { getOverviewDataApi } from '@/api/services/dataProfile'
import { formatTime } from '@/utils/index'

export default {
  name: 'profileData',
  components: {
    totalData
  },
  mixins: [dateOptionsMixins],
  data() {
    const nowDate = Date.now(),
      oneDay = 86400 * 1000

    return {
      loading: false, // 加载loading
      time: [new Date(nowDate - oneDay), new Date(nowDate - oneDay)], // 日期筛选

      adTypeOptions: [
        {
          label: '曝光量UV',
          value: 0
        },
        {
          label: '点击量',
          value: 1
        },
        {
          label: '有效点击量',
          value: 2
        }
      ],

      // 用户增长数据
      userGrowthData: [
        {
          name: '新增用户数',
          value: 0,
          link: 'platformUserData',
          key: 'addUser'
        }
      ],
      // 积分用户增长数据
      pointsUserGrowthData: [
        {
          name: '首次获取积分用户',
          value: 0,
          link: 'IntegralManage',
          key: 'firstReceive'
        },
        {
          name: '首次积分兑换用户',
          value: 0,
          link: 'IntegralManage',
          key: 'firstPayByPointsUnionIdCount',
          params: {
            activeName: 'exchangePoint'
          }
        },
        {
          name: '首次积分+现金购买用户',
          value: 0,
          link: 'IntegralManage',
          key: 'firstPayByPointsAndMoneyUnionIdCount',
          params: {
            activeName: 'exchangePoint'
          }
        }
      ],
      // 钱包用户增长数据
      walletUserGrowthData: [
        {
          name: '首次积分互换客户',
          value: 0,
          link: 'aiduifenUserData',
          key: 'pointsExchange',
          params: {
            activeName: 'firstExchangeData'
          }
        }
      ],
      // 广告数据展示
      adData: [
        {
          name: '曝光量(UV)',
          value: 0,
          key: 'uv'
        },
        {
          name: '有效点击量',
          value: 0,
          key: 'effectiveCv'
        },
        {
          name: '点击量',
          value: 0,
          key: 'cv'
        }
      ]
    }
  },
  mounted() {
    this.getOverviewData()
  },
  methods: {
    // 获取数据概览数据
    getOverviewData() {
      let _this = this,
        time = _this.time,
        params = {}

      if (time) {
        params.startAt = formatTime(time[0], 'yyyyMMdd')
        params.endAt = formatTime(time[1], 'yyyyMMdd')
      }

      _this.loading = true

      getOverviewDataApi(params).then(res => {
        let handlerDataList = [_this.adData, _this.userGrowthData, _this.pointsUserGrowthData, _this.walletUserGrowthData],
          increaseData = res.increaseData, // 增长数据
          // 聚合数据
          formatIncreaseData = {
            addUser: increaseData.addUser, // 新增用户数
            ...increaseData.first,
            ...increaseData.wallet,
            ...res.adData
          }

        // 根据字段值动态赋值
        handlerDataList.forEach(handlerItem => {
          handlerItem.forEach(item => {
            let currentValue = formatIncreaseData[item.key] || 0

            item.value = currentValue
          })
        })

        _this.loading = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.profile-data {
  .group {
    border-radius: 8px;
    margin: 0 16px;

    & + .group {
      margin-top: 16px;
    }

    .group-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 24px 16px;
      background: #fff;
      border-radius: 8px;
      margin-bottom: 16px;

      .group-title {
        display: flex;
        align-items: flex-end;
        color: #333;

        h1 {
          margin-right: 8px;
          font-size: 20px;
        }

        .tips {
          font-size: 12px;
        }
      }

      .screen {
        .el-button {
          margin-left: 16px;
        }
      }
    }

    &.grown-data {
      .grown-data-list {
        display: flex;

        &.grown-data-list {
          margin-top: 16px;
        }

        .grown-data-item {
          width: 100%;
          background: #fff;
          border-radius: 8px;

          .title {
            padding: 24px 16px;
            color: #333;
            font-size: 16px;
          }

          &.user {
            width: 30%;
            margin-right: 16px;
          }

          &.points-user {
            width: 70%;
          }
        }
      }

    }

    &.ad-data {
      .group-content {
        background: #fff;
        border-radius: 8px;
      }
    }
  }
}
</style>
