// 漏斗columns
export function funnelColumns(funnelData = {}, homePagePv = 0) {
  const accredit = (accreditVal, noAccreditVal) => ({
    accreditName: '已授权用户',
    noAccreditName: '未授权用户',
    accreditVal,
    noAccreditVal
  })
  return {
    // 兑换商品数据
    goodsFunnel: [
      {
        ...accredit(funnelData.goodsDetailVisitorAuthNumber || 0, (homePagePv - funnelData.goodsDetailVisitorAuthNumber) || 0),
        per: funnelData.goodsDetailVisitorNumberPre,
        value: funnelData.goodsDetailVisitorNumber,
        name: '访问商品详情页人数'
      },
      {
        ...accredit(funnelData.indexVisitorAuthNumber || 0, (homePagePv - funnelData.indexVisitorAuthNumber) || 0),
        per: funnelData.indexVisitorNumberPre,
        value: funnelData.indexVisitorNumber,
        name: '首次访问用户数'
      },
      {
        value: funnelData.clickUseGoldenNumber,
        name: '点击跳转拼多多人数',
        per: funnelData.clickUseGoldenNumberPre
      },
      {
        value: funnelData.tradeSuccessNumber,
        per: funnelData.tradeSuccessNumberPre,
        name: '交易成功'
      }
    ],
    // 兑换劵数据
    couponFunnel: [
      {
        value: funnelData.cardIndexVisitorNumber,
        name: '首次访问用户数',
        per: funnelData.cardIndexVisitorNumberPre
      },
      {
        value: funnelData.clickUseCouponNumber,
        name: '点击使用人数',
        per: funnelData.clickUseCouponNumberPre
      },
      {
        value: funnelData.clickCardNumber,
        name: '点翻牌人数',
        per: funnelData.clickCardNumberPre
      },
      {
        value: funnelData.cardCouponNumber,
        name: '兑换劵人数',
        per: funnelData.cardCouponNumberPre
      }
    ],
    // 邀请好友数据
    friendFunnel: [
      {
        value: funnelData.stealNumber + funnelData.fetchNumber,
        name: '领取金豆人数/偷金豆人数',
        per: funnelData.stealFetchPre
      },
      {
        value: funnelData.inviteNumber,
        name: '点击邀请分享到微信人数',
        per: funnelData.inviteNumberPre
      },
      {
        value: funnelData.beInvitedNumber,
        name: '被邀请人进来数',
        per: funnelData.beInvitedNumberPre
      },
      {
        value: funnelData.goldenIndexVisitorNumber,
        name: '首次访问用户数',
        per: funnelData.goldenIndexVisitorNumberPre
      }
    ]
  }
}

// 统计数据columns
export function totalColumns() {
  return [
    {
      label: '首页访问PV',
      key: 'homePagePv',
      value: ''
    },
    {
      label: '访问总人数',
      key: 'indexVisitorNumber',
      value: ''
    },
    {
      label: '积分用户数',
      key: 'userNumber',
      value: ''
    },
    {
      label: '购买商品人数',
      key: 'orderNumber',
      value: ''
    },
    {
      label: '兑换商品人数',
      key: 'couponNumber',
      value: ''
    },
    {
      label: '发金豆数',
      key: 'expenditure',
      value: ''
    },
    {
      label: '回收金豆数',
      key: 'income',
      value: ''
    }
  ]
}
