<template>
  <div>
    <div class="container">
      <Title-item title="订单数据"></Title-item>
      <Total-data :totalList="orderData"></Total-data>
    </div>
    <!--关键数据-->
    <div class="container">
      <Title-item title="关键数据" type="search" @handleData="search"></Title-item>
      <Total-data :totalList="cruxData">
        <template slot-scope="row">
          <div v-if="row.scope.state" class="percent">
            <span>环比增长</span>&nbsp;
            <span :style="row.scope.per >= 0 ? colorRed : colorGreen">{{row.scope.per | symbol}}{{row.scope.per | percent}}%</span>
          </div>
        </template>
      </Total-data>
    </div>
  </div>
</template>

<script>
import TotalData from '../components/TotalData'
import TitleItem from '../components/TitleItem'
import { getIndicators } from '@/api/services/dataProfile'
import moment from 'moment'
import { toFixed } from '@/utils'

const DATE = new Date()
let beforeTime, BEFORE_DATE
beforeTime = day => DATE - 86400 * day * 1000 // day 获取多少天之前的
BEFORE_DATE = moment(beforeTime(1)).format('YYYYMMDD') // 昨天
export default {
  name: 'index',
  components: {
    TotalData,
    TitleItem
  },
  filters: {
    percent(val) {
      return toFixed(val * 100)
    },
    symbol(val) {
      return val >= 0 ? '+' : ''
    }
  },
  data() {
    return {
      time: '',
      searchTime: [
        '昨日',
        '最近7天',
        '最近30天'
      ],
      params: {
        startAt: BEFORE_DATE,
        endAt: BEFORE_DATE
      },
      colorRed: { color: '#F26161' },
      colorGreen: { color: '#7BCC52' },
      cruxData: [
        {
          label: '首页访问pv',
          value: 0,
          per: 0,
          perKey: 'rosePv',
          key: 'homePagePv',
          state: true
        },
        {
          label: '首页访问uv',
          value: 0,
          perKey: 'roseUv',
          per: 0,
          key: 'homePageUv',
          state: true
        },
        {
          label: '日留存率',
          value: 0,
          key: 'reteRates'
          // state: true
        },
        {
          label: '已支付金额',
          key: 'paymentTotal',
          value: 0
        },
        {
          label: '回收积分总数',
          key: 'pointsTotal',
          value: 0
        }
      ],
      orderData: [
        {
          label: '待发货订单',
          key: 'waitOrderCount',
          // path: '/orderList?orderStatus=30',
          value: 0
        },
        {
          label: '退货售后订单',
          key: 'afterSaleCount',
          // path: '/orderList?orderStatus=40',
          value: 0
        },
        {
          label: '待上架商品',
          key: 'stayOnGoodsQuantity',
          // path: '/ProductList?status=0',
          value: 0
        },
        {
          label: '已售完商品',
          key: 'soldOutGoodsQuantity',
          // path: '/ProductList?status=2',
          value: 0
        }
      ]
    }
  },
  created() {
    this.getDataList(this.params)
  },
  methods: {
    search(params) {
      this.getDataList(params)
    },
    getDataList(param) {
      getIndicators(param).then(res => {
        if (!res) return
        const filter = (value, key) => {
          const obj = {
            paymentTotal: toFixed(value / 100),
            reteRates: toFixed(value * 100) + '%'
          }
          return obj[key] || value
        }
        let { orderRemind, keyData } = res
        this.orderData.forEach(v => {
          v.value = orderRemind[v.key]
        })
        this.cruxData.forEach(v => {
          const value = keyData[v.key]
          v.value = filter(value, v.key)
          v.per = keyData[v.perKey]
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
  .title {
    color: #333333;
    font-family: PingFangSC-Regular;
    font-size: 20px;
    font-weight: normal;
  }

  .container {
    background: #fff;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 24px 16px;

    .percent {
      span {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #606166;
      }
    }

    .card_title_container {
      display: flex;
      justify-content: space-between;
    }
  }
</style>
