<template>
  <div class="profile-data" v-loading="loading">
    <div class="group active-data">
      <div class="group-head">
        <div class="group-title">
          <h1>活跃数据</h1>
          <span class="tips">(当天默认显示昨天数据)</span>
        </div>

        <div class="screen">
          <el-date-picker
            v-model="time"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :clearable="false"
            :picker-options="pickerOptions">
          </el-date-picker>

          <el-button type="primary" @click="getData">查询</el-button>
        </div>
      </div>

      <div class="group-content">
        <totalData :data="activeData"></totalData>
      </div>
    </div>
  </div>
</template>

<script>
import totalData from './totalData'
import { formatTime } from '@/utils/index'
import dateOptionsMixins from '@/components/dateOptionsMixins'
import { getActiveCustomerApi } from '@/api/services/dataProfile'

export default {
  name: 'profileData',
  mixins: [dateOptionsMixins],
  components: {
    totalData
  },
  data() {
    const nowDate = Date.now(),
      oneDay = 86400 * 1000

    return {
      loading: false, // 加载loading
      time: [new Date(nowDate - oneDay), new Date(nowDate - oneDay)], // 日期筛选
      activeData: [
        {
          name: '活跃用户数',
          key: 'activeUser',
          value: 0,
          link: 'platformActiveData'
        },
        {
          name: '活跃积分用户数',
          key: 'activePointUser',
          value: 0,
          link: 'platformDigitalActiveData'
        },
        {
          name: '活跃客户数',
          key: 'activeCustomer',
          value: 0,
          link: 'aiduifenActiveData'
        },
        {
          name: '活跃积分互换客户',
          key: 'pointExchangeCustomer',
          value: 0,
          link: 'aiduifenActiveData',
          params: {
            activeName: 'exchangeActiveData'
          }
        }
      ]
    }
  },
  created() {
    this.getData()
  },
  methods: {
    // 获取数据
    getData() {
      let _this = this,
        time = _this.time,
        params = {}

      if (time) {
        params.startAt = formatTime(time[0], 'yyyyMMdd')
        params.endAt = formatTime(time[1], 'yyyyMMdd')
      }

      _this.loading = true

      getActiveCustomerApi(params).then(res => {
        let activeData = _this.activeData

        activeData.forEach(item => {
          item.value = res[item.key] || 0
        })

        _this.loading = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.profile-data {
  .group {
    border-radius: 8px;
    margin: 0 16px;

    & + .group {
      margin-top: 16px;
    }

    .group-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 24px 16px;
      background: #fff;
      border-radius: 8px;
      margin-bottom: 16px;

      .group-title {
        display: flex;
        align-items: flex-end;
        color: #333;

        h1 {
          margin-right: 8px;
          font-size: 20px;
        }

        .tips {
          font-size: 12px;
        }
      }
    }

    &.active-data {
      background: #fff;

      .group-head {
        margin-bottom: 0;
      }
    }
  }
}
</style>
