<template>
  <div class="invite-detail-wrap">

    <div class="section-wrap">
      <div class="title-wrap">
        <h1 class="title">
          平台: {{detailInfo.platformType}}<span class="username">用户昵称: {{detailInfo.nickName}}</span>
        </h1>

        <el-button class="title-btn" type="primary" @click="exportData">导出</el-button>
      </div>

      <p class="invite-num">成功邀请人数: {{totalInviteAward}}</p>

      <el-table class="table-wrap"
        header-row-class-name="table-header"
       :data="tableDataList">
        <el-table-column
          prop="regTime"
          align="center"
          label="日期">
        </el-table-column>
        <el-table-column
          align="center"
          prop="nickName"
          label="邀请好友昵称">
        </el-table-column>
        <el-table-column
          align="center"
          prop="inviteAward"
          label="积分明细">
          <template slot-scope="scope">
            {{scope.row.inviteAward > 0 ? `+${scope.row.inviteAward}` : 0}}
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="(totalCount / pageSize)<= 1"
        :page-size="pageSize"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getInviteSendPointsDetailData, exportDataDefault } from '@/api/services/integralActiveManage'

export default {
  name: 'invite-detail',
  components: {},
  data() {
    return {
      tableDataList: [],
      detailInfo: {},
      totalInviteAward: 0, // 成功邀请人数
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      page: 1
    }
  },
  created() {
    let _this = this

    _this.detailInfo = _this.$route.query
    _this.getListData()
  },

  methods: {
    getListData() {
      let _this = this,
        params = {
          appletId: _this.detailInfo.appletId,
          unionId: _this.detailInfo.unionId,
          pageSize: _this.pageSize,
          page: _this.page
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getInviteSendPointsDetailData(params).then(res => {
          _this.tableDataList = res.list
          _this.totalInviteAward = res.totalInviteAward
          _this.totalCount = Number(res.totalCount)
          _this.pageSize = Number(res.pageSize)
          _this.isPost = false
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.detailInfo.appletId,
          unionId: _this.detailInfo.unionId,
          pageSize: _this.pageSize,
          page: _this.page,
          isExport: 1
        }

      exportDataDefault('inviteSendDetail', params)
    },

    handleSizeChange(size) {
      this.pageSize = size
    },

    handleCurrentChange(page) {
      let _this = this

      _this.page = page
      _this.getListData()
    }
  }
}
</script>

<style lang="less" scoped>
.invite-detail-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;

    .title-wrap {
      border-bottom: 1px solid #F2F3F7;
      padding-bottom: 12px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .title {
        font-size: 20px;
        line-height: 28px;

        .username {
          margin-left: 16px;
          font-size: 16px;
        }
      }
    }

    .invite-num {
      margin-top: 16px;
      font-size: 24px;
      font-weight: bold;
      line-height: 28px;
    }

    .table-wrap {
      margin-top: 24px;
    }
    .pagination-wrap {
      margin-top: 16px;
      text-align: right;
    }
  }
}
</style>
