<template>
  <div class="funnel_container">
    <h3>{{title}}</h3>
    <div :class="'funnel_item'+index" v-for="(item, index) in dataList" :key="index">
      <span>{{item.name}}</span>
      <el-tooltip v-if="item.accreditName || item.noAccreditName" class="item" effect="dark" placement="left-start">
        <div slot="content">
          {{item.accreditName}} : {{item.accreditVal}} <br/> <br/>
          {{item.noAccreditName}} : {{item.noAccreditVal}}
        </div>
        <div></div>
      </el-tooltip>
      <div class="funnel_data">
        <p class="funnel_data_number">{{item.value || 0}}</p>
        <p class="f12">环比增长</p>
        <div class="funnel_data_per">
          <span>{{item.per | abs}}%</span>
          <i :class="className(item.per)"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { toFixed } from '@/utils'

export default {
  name: 'Funnel',
  props: ['dataList', 'title'],
  data() {
    return {}
  },
  filters: {
    abs(data) {
      const result = data < 0 ? Math.abs(data) : data
      return toFixed(result * 100)
    }
  },
  methods: {
    className(val) {
      return (val >= 0) ? 'el-icon-top' : 'el-icon-bottom down'
    }
  }
}
</script>

<style lang='less' scoped>
  .funnel_container {
    display: flex;
    align-items: center;
    flex-direction: column;

    .item {
      position: absolute;
      top: 0;
      background: transparent;
      width: 100%;
      height: 100%;
    }

    > h3 {
      font-weight: normal;
      margin-bottom: 24px;
    }

    > div {
      position: relative;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;

      > span {
        position: absolute;
        color: #fff;
        font-size: 14px;
      }

      > .funnel_data {
        position: absolute;
        width: 90px;
        height: 100%;
        right: -120px;

        .f12 {
          font-size: 12px;
        }

        > .funnel_data_number {
          font-size: 20px;
          line-height: 1.5em;
        }

        > .funnel_data_per {
          font-size: 12px;

          > i {
            font-size: 16px;
            color: #45B07F;
            vertical-align: middle;
          }

          > .down {
            color: red;
          }
        }
      }
    }

    > div::before {
      height: 60px;
      content: '';
      transform: perspective(30px) rotateX(-2deg);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .funnel_item0 {
      width: 280px;

      &::before {
        background: #55B6FF;
      }
    }

    .funnel_item1 {
      width: 240px;

      &::before {
        background: #45B07F;
      }
    }

    .funnel_item2 {
      width: 200px;

      &::before {
        background: #F38E3B;
      }
    }

    .funnel_item3 {
      width: 160px;

      &::before {
        background: #DC5B57;
      }
    }
  }
  h3, p, span {
    color: #000;
  }
</style>
