import platformUserData from './components/platformUserData'
import platformUserDataDetail from './components/platformDataDetail'

import platformActiveData from './components/platformActiveData'
import platformDigitalActiveData from './components/platformDigitalActiveData'
import userDigitalDetail from './components/userDigitalDetail'

// 平台用户管理分路由
import platformUserManage from './index'

export default {
  path: '/platform-user-manage',
  redirect: {
    name: 'platformUserData'
  },
  component: platformUserManage,
  children: [
    {
      path: 'platform-user-data',
      name: 'platformUserData',
      component: platformUserData
    },
    {
      path: 'platform-active-data',
      name: 'platformActiveData',
      component: platformActiveData
    },
    {
      path: 'platform-digittal-active-detail',
      name: 'platformDigitalActiveData',
      component: platformDigitalActiveData
    },
    {
      path: 'platform-user-data-detail',
      name: 'platformUserDataDetail',
      component: platformUserDataDetail
    },
    {
      path: 'platform-user-digital-detail',
      name: 'platformUserDigitalDetail',
      component: userDigitalDetail
    }
  ]
}
