<template>
  <div class="user-detail-info-wrap">
    <div class="section-wrap">
      <h1 class="title">用户数据-用户详情</h1>
      <div class="detail-info-wrap">
        <img class="avator" v-if="userDetailInfo.avatar" :src="userDetailInfo.avatar" alt="用户图像Logo" />
        <div class="user-info">
          <p class="info">UnionID: {{userDetailInfo.unionId}}</p>
          <p class="info">用户昵称: {{userDetailInfo.nickName}}</p>
          <p class="info">
            <span>注册时间: {{userDetailInfo.registerTime}}</span>
            <span>手机号码: {{userDetailInfo.phone || ''}}</span>
            <span>平台: {{userDetailInfo.paltformType}}</span>
          </p>
        </div>
      </div>
    </div>

    <div class="section-wrap">
      <h1 class="title">积分资产</h1>
      <div class="digital-info-wrap">
        <div class="item">
          <p class="type-name">米粒</p>
          <p class="type-num">{{pointsAssert.riceAmount}}</p>
        </div>
        <div class="item">
          <p class="type-name">ADF数量</p>
          <p class="type-num">{{pointsAssert.adfAmount}}</p>
        </div>
        <div class="item">
          <p class="type-name">GBT数量</p>
          <p class="type-num">{{pointsAssert.gbtAmount}}</p>
        </div>
      </div>
    </div>

    <div class="section-wrap">
      <h1 class="title">现金流资产</h1>
      <div class="digital-info-wrap">
        <div class="item">
          <p class="type-name">已支付金额</p>
          <p class="type-num">{{cashFlowAssets.havePaid}}</p>
        </div>
        <div class="item">
          <p class="type-name">提现金额</p>
          <p class="type-num">{{cashFlowAssets.withdrawAmount}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getPlatformUserDetail } from '@/api/services/platformUserManage'

export default {
  name: 'platform-user-detail',
  components: {},
  data() {
    return {
      pointsAssert: {
        adfAmount: 0,
        gbtAmount: 0,
        riceAmount: 0
      },
      cashFlowAssets: {
        havePaid: 0,
        withdrawAmount: 0
      },
      userDetailInfo: {} // 用户信息
    }
  },
  created() {
    let _this = this

    _this.userDetailInfo = JSON.parse(sessionStorage.getItem('userManageDetail'))
    _this.getDetailData()
  },

  destroyed() {
    sessionStorage.getItem('userManageDetail') && sessionStorage.removeItem('userManageDetail')
  },
  methods: {
    getDetailData() {
      let _this = this,
        params = {
          appletId: _this.userDetailInfo.appletId,
          unionId: _this.userDetailInfo.unionId
        }

      return new Promise((resolve, reject) => {
        getPlatformUserDetail(params).then(res => {
          _this.pointsAssert = res.pointsAssert
          _this.cashFlowAssets = res.cashFlowAssets
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.user-detail-info-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;

    .title {
      padding-bottom: 12px;
      font-size: 20px;
      line-height: 28px;
      border-bottom: 1px solid #F2F3F7;
    }

    .detail-info-wrap {
      display: flex;
      margin-top: 16px;

      .avator {
        display: block;
        width: 125px;
        height: 125px;
      }

      .user-info {
        margin-left: 16px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .info {
          padding-top: 8px;
          font-size: 14px;
          color: #606166;

          span {
            margin-right: 16px;
          }
        }
      }
    }

    .digital-info-wrap {
      display: flex;
      align-items: center;

      .item {
        padding: 24px 16px 0px;
        min-width: 150px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .type-name {
          font-size: 14px;
          color: #606166;
        }

        .type-num {
          font-size: 40px;
          line-height: 56px;
          color: #4D5AFF;
        }
      }
    }

  }
}
</style>
