import totalDataProfile from './totalProfile'
import juyiProfile from './juyiProfile'
import hshProfile from './hshProfile'
import dataProfile from './index'

export default {
  path: '',
  component: dataProfile,
  children: [
    {
      path: '',
      name: 'totalDataProfile',
      component: totalDataProfile
    },
    {
      path: 'dataProfile-juyiProfile',
      name: 'juyiProfile',
      component: juyiProfile
    },
    {
      path: 'dataProfile-hshProfile',
      name: 'hshProfile',
      component: hshProfile
    }
  ]
}
