<template>
  <div class="good-list" v-loading="loading">
    <h2 class="title">拼多多商品列表</h2>

    <div class="query">
      <div class="query-type">
        <div
          class="query-type-item"
          v-for="item in qeuryStatusList"
          :class="{'active': query.onSale === item.value}"
          :key="item.label"
          @click="handerQueryType(item.value)">
          {{item.label}}
        </div>
      </div>

      <div class="query-list">
        <el-select
          class="query-list-item"
          v-model="query.appletId"
          placeholder="请选择商家类型"
          @change="handleScreen">
          <el-option
            v-for="(item, key) in appletList"
            :key="item"
            :label="item"
            :value="key">
          </el-option>
        </el-select>

        <el-input v-model="query.goodsName" class="query-list-item keyword" placeholder="输入商品名称"></el-input>
        <el-button class="query-list-item" type="primary" @click="handleScreen">搜索</el-button>
        <el-button class="query-list-item" type="primary" @click="exportData">导出</el-button>
      </div>
    </div>

    <el-table :data="tableData">
      <el-table-column label="商品ID" prop="goodsId"></el-table-column>
      <el-table-column label="商品名称" prop="goodsName" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="平台">
        <template slot-scope="scope">
          {{ appletList[scope.row.appletId] }}
        </template>
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          {{scope.row.onSale == 1 ? '在售' : '仓库'}}
        </template>
      </el-table-column>
      <el-table-column label="商品类型">
        <template>
          拼多多
        </template>
      </el-table-column>
      <el-table-column label="优惠券" prop="couponDiscount"></el-table-column>
      <el-table-column label="优惠券库存" prop="couponInfo.couponRemainQuantity"></el-table-column>
      <el-table-column label="原价" prop="formatMinNormalPrice"></el-table-column>
      <el-table-column label="积分消耗">
        <template slot-scope="scope">
          {{ scope.row.pointsInfo.amount || 0 }}
        </template>
      </el-table-column>
      <el-table-column label="佣金比例">
        <template slot-scope="scope">
          {{ scope.row.formatPromotionRate }}
        </template>
      </el-table-column>
      <el-table-column label="返现比">
        <template slot-scope="scope">
          {{ (scope.row.pointsInfo.promotionRate || 0) / 100 }} / {{ (scope.row.cnyInfo.promotionRate || 0) / 100 }}
        </template>
      </el-table-column>
      <el-table-column label="商品销量" prop="salesTip"></el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :hide-on-single-page="true"
        :current-page.sync="page.currentPage"
        :page-size.sync="page.pageSize"
        layout="prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPddGoodListApi, exportPddGoodListApi } from '../../../api/services/pointsMallManagement'
import config from '@/config'

export default {
  data() {
    return {
      loading: false, // 加载loading
      query: {
        onSale: 2, // 商品状态
        appletId: '1', // 商家类型
        goodsName: '' // 关键词筛选
      },
      page: {
        total: 0, // 总条数
        currentPage: 1,
        pageSize: 20
      },
      // 商品状态列表
      qeuryStatusList: [
        {
          label: '全部商品',
          value: 2
        },
        {
          label: '在售商品',
          value: 1
        },
        {
          label: '仓库中',
          value: 0
        }
      ],
      // 商家类型列表、
      appletList: config.appletList,
      tableData: [] // 表格数据
    }
  },
  created() {
    this.getDataList()
  },
  methods: {
    // 获取表格数据
    getDataList() {
      let _this = this

      _this.loading = true

      getPddGoodListApi(_this.getQueryData()).then(res => {
        let page = _this.page

        res.data.forEach(goodItem => {
          let couponInfo = goodItem.couponInfo,
            couponDiscount = couponInfo.couponDiscount, // 优惠券金额
            minNormalPrice = goodItem.minNormalPrice, // 原价
            salesConfig = goodItem.salesConfig,
            promotionRate = goodItem.promotionRate, // 佣金比例
            pointsInfo = {}, // gbt支付信息
            cnyInfo = {} // 人命币支付信息

          goodItem.couponDiscount = couponDiscount ? Number(couponDiscount) / 100 + '元' : ''
          goodItem.formatMinNormalPrice = minNormalPrice ? Number(minNormalPrice) / 100 + '元' : ''
          goodItem.formatPromotionRate = promotionRate / 10 + '%'

          salesConfig.forEach(salesConfigItem => {
            if (salesConfigItem.type === 'CNY') {
              cnyInfo = salesConfigItem
            } else {
              pointsInfo = salesConfigItem
            }
          })

          goodItem.pointsInfo = pointsInfo
          goodItem.cnyInfo = cnyInfo
        })

        _this.tableData = res.data
        page.total = Number(res.total)

        _this.loading = false
      }).catch(() => {
        _this.loading = false
      })
    },

    // 处理类型筛选
    handerQueryType(status) {
      this.query.onSale = status
      this.handleScreen()
    },

    // 处理每页条数改变
    handleScreen(pageSize) {
      let _this = this

      _this.page.currentPage = 1
      _this.getDataList()
    },

    /**
     * 获取筛选数据
     * @param { Boolean } type 类型  screen 筛选 export 导出
     */
    getQueryData(type = 'screen') {
      let _this = this,
        page = _this.page,
        query = _this.query,
        goodsName = query.goodsName,
        onSale = query.onSale,
        params = {
          appletIds: [query.appletId],
          page: page.currentPage
        }

      if (goodsName) {
        params.goodsName = goodsName
      }

      if (onSale !== '') {
        params.onSale = onSale
      }

      if (type === 'export') {
        params.isExport = 1
        params.appletIds = String(params.appletIds)
      }

      return params
    },

    // 导出
    exportData() {
      exportPddGoodListApi(this.getQueryData('export'))
    },

    // 处理分页
    handleCurrentChange() {
      this.getDataList()
    }
  }
}
</script>

<style lang="less" scoped>
.good-list {
  padding: 0 16px;
  border-radius: 8px;
  background: #fff;

  & > .title {
    padding: 24px 0;
    font-size: 20px;
    line-height: 28px;
    color: #333;
  }

  .query {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #333;
    padding-bottom: 16px;

    .query-type {
      display: flex;
      align-items: center;

      .query-type-item {
        position: relative;
        padding: 16px 0;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 0;
        cursor: pointer;

        & + .query-type-item {
          margin-left: 40px;
        }

        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 2px;
          background: #4D5AFF;
        }

        &.active {
          color: #4D5AFF;

          &::after {
            display: block;
          }
        }
      }
    }

    .query-list {
      display: flex;
      align-items: center;

      .query-list-item {
        & + .query-list-item {
          margin-left: 16px;
        }

        &.keyword {
          width: 164px;
        }
      }
    }
  }

  .pagination {
    padding-bottom: 16px;
    text-align: right;
  }
}
</style>
