<template>
  <div class="first-exchange-data">
    <div class="search">
      <el-date-picker
        v-model="rangeDate"
        class="date-picker"
        type="daterange"
        @change="handleDateChange"
        align="left"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        value-format="yyyy-MM-dd">
      </el-date-picker>

      <el-input class="header-search"
        v-model="keyword"
        placeholder="请输入用户手机号、昵称进行搜索"
        clearable
        @clear="inputClear">
      </el-input>

      <el-button type="primary" @click="searchKey">查询</el-button>
      <el-button type="success" class="export-btn" @click="exportData">导出</el-button>
    </div>
    <div class="num-box">
      <p>首次积分互换客户</p>
      <p class="num">{{firstExchangeTotal}}</p>
    </div>

    <p class="tab-title">用户列表（当天默认显示昨天数据）</p>
    <div class="table">
      <el-table :data="tableData" border>
        <el-table-column show-overflow-tooltip label="首次互换日期" prop="entrustedAt"></el-table-column>
        <el-table-column show-overflow-tooltip label="UnionId" prop="wxUnionId"></el-table-column>
        <el-table-column show-overflow-tooltip label="用户昵称" prop="nickname"></el-table-column>
        <el-table-column show-overflow-tooltip label="手机号码" prop="mobile"></el-table-column>
        <el-table-column show-overflow-tooltip label="身份证验证" prop="isAuthenticated"></el-table-column>
        <el-table-column show-overflow-tooltip label="身份证号" prop="identificationNumber"></el-table-column>
        <el-table-column show-overflow-tooltip label="互换方式" prop="modeType"></el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination-wrap" v-if="parseInt(total) > 1">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="pageSize"
            layout="total, prev, pager, next, jumper"
            :total="parseInt(total)">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { firstExchange } from '../../../api/services/aiduifen'
import urls from '@/config/urls'
import { formatTime } from '@/utils/index'

export default {
  data() {
    return {
      page: 0,
      pageSize: 20, // 每页显示多少条数据
      total: 0, // 总条数
      totalPage: 0, // 总页数
      activeName: 'firstExchangeData',
      rangeDate: '',
      keyword: '',
      tableData: [], // 表格数据
      firstExchangeTotal: 0 // 首次积分互换客户
    }
  },
  mounted() {
    let nowDate = new Date(),
      yesterday
    yesterday = formatTime(nowDate.setTime(nowDate.getTime() - 24 * 60 * 60 * 1000), 'yyyy-MM-dd')
    this.rangeDate = []
    this.rangeDate[0] = yesterday
    this.rangeDate[1] = yesterday
    this.getExchangeData(this.getQueryData())
  },
  methods: {
    // 获取新增数据列表
    getExchangeData(params) {
      let _this = this
      firstExchange(params).then(res => {
        _this.tableData = res.list
        _this.firstExchangeTotal = res.totalCount
        _this.total = Number(res.totalCount)
      })
    },
    // 获取筛选数据
    getQueryData() {
      let time = this.rangeDate,
        params = {
          page: this.page,
          pageSize: this.pageSize,
          searchKey: this.keyword === '' ? undefined : this.keyword
        }
      if (time) {
        params.startAt = `${time[0]} 00:00:00`
        params.endAt = `${time[1]} 23:59:59`
      }
      return params
    },
    // 选择时间
    handleDateChange() {
      this.page = 0
      this.getExchangeData(this.getQueryData())
    },
    // 输入框清空
    inputClear() {
      this.keyword = ''
      this.page = 0
      this.getExchangeData(this.getQueryData())
    },
    searchKey() {
      this.page = 0
      this.getExchangeData(this.getQueryData())
    },
    // 导出
    exportData() {
      let _startAt = '',
        _endAt = '',
        downloadUrl = '',
        searchKey = this.keyword ? this.keyword : '',
        url = `${urls.backend}/wallet/users-data/first-exchange`
      if (this.rangeDate && this.rangeDate.length > 0) {
        _startAt = this.rangeDate[0] ? `${this.rangeDate[0]} 00:00:00` : ''
        _endAt = this.rangeDate[1] ? `${this.rangeDate[1]} 23:59:59` : ''
      }
      downloadUrl = `${url}?t=${sessionStorage.getItem('t')}&page=0&startAt=${_startAt}&endAt=${_endAt}&searchKey=${searchKey}&isExport=1`
      window.open(downloadUrl)
    },
    // 点击分页，获取数据
    handleCurrentChange(curPage) {
      this.page = curPage - 1
      this.getExchangeData(this.getQueryData())
    }
  }
}
</script>
