<template>
  <div class="wrap">
    <el-tabs v-model="activeName">
      <el-tab-pane label="积分结算" name="Settle">
        <div class="form-inlne">
          <create-form :formConfig="formConfigSettle" @submit="submetSettle"></create-form>
          <el-button type="primary" style="margin-left:20px" @click="dowloadSettle">导出</el-button>
        </div>
        <create-card :data="cardDataSettle"></create-card>
        <p>积分明细</p>
        <create-table
          :tableData="tableDataSettle"
          :tableConfig="tableConfigSettle"
          :totalCount="totalSettle"
          @change="changeSettle"
        ></create-table>
      </el-tab-pane>
      <el-tab-pane label="积分资产" name="Assets">
        <div class="form-inlne">
          <create-form :formConfig="formConfigAssets" @submit="submitAssets"></create-form>
          <el-button type="primary" style="margin-left:20px" @click="dowloadAssets">导出</el-button>
        </div>
        <create-card :data="cardDataAssets"></create-card>
        <p>用户列表</p>
        <create-table
          :tableData="tableDataAssets"
          :tableConfig="tableConfigAssets"
          :totalCount="totalAssets"
          @change="changeAssets"
        ></create-table>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import createForm from '../common/CreateForm'
import createCard from '../common/CreateCard'
import createTable from '../common/CreateTable'
import {
  getPointsSettle,
  getPointsTotalValue,
  downloadSettle,
  downloadAssets
} from '../../../api/services/assetsManage'

export default {
  data() {
    return {
      activeName: 'Settle',
      // 积分结算数据
      totalSettle: 0,
      paramSettle: {
        startTime: Math.floor(
          (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
        ),
        endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1),
        appletId: 1
      },
      formConfigSettle: [
        {
          type: 'select',
          key: 'appletId',
          value: 1,
          options: [{ label: '很实惠', value: 1 }, { label: '聚驿', value: 2 }]
        },
        {
          type: 'datePicker',
          key: 'date',
          value: [
            new Date(+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60),
            new Date(Math.floor(+new Date().setHours(0, 0, 0, 0)) - 1)
          ]
        }
      ],
      cardDataSettle: [
        { label: '发放积分', value: 0 },
        { label: '回收积分', value: 0 },
        { label: '未回收积分', value: 0 }
      ],
      tableDataSettle: [],
      tableConfigSettle: [
        { prop: 'buyDate', label: '日期' },
        { prop: 'distributionPoints', label: '发放积分数', sortable: true },
        { prop: 'recoveryPoints', label: '回收积分数', sortable: true },
        { prop: 'nodeGetPoints', label: '平台积分' },
        { prop: 'userGetPoints', label: '用户积分' },
        { prop: 'pointsType', label: '积分类型' }
      ],
      // 积分资产数据
      totalAssets: 0,
      paramAssets: {
        startTime: Math.floor(
          (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
        ),
        endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1),
        appletId: 1
      },
      formConfigAssets: [
        {
          type: 'select',
          key: 'appletId',
          value: 1,
          options: [{ label: '很实惠', value: 1 }, { label: '聚驿', value: 2 }]
        },
        {
          type: 'datePicker',
          key: 'date',
          value: [
            new Date(+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60),
            new Date(Math.floor(+new Date().setHours(0, 0, 0, 0)) - 1)
          ]
        }
      ],
      cardDataAssets: [
        { label: '积分发放总额', value: 0 },
        { label: '积分回收消耗总额', value: 0 },
        { label: '积分发放总资产', value: 0 },
        { label: '积分回收消耗总资产', value: 0 }
      ],
      tableDataAssets: [],
      tableConfigAssets: [
        { prop: 'date', label: '日期' },
        {
          prop: 'distributionGBTTotalVale',
          label: '积分发放金额',
          formatter(row) {
            return (row.distributionGBTTotalVale / 100).toFixed(2)
          }
        },
        {
          prop: 'recoveryPointsGBTTotalVale',
          label: '积分回收消耗金额',
          formatter(row) {
            return (row.recoveryPointsGBTTotalVale / 100).toFixed(2)
          }
        },
        {
          prop: 'distributionADFTotalVale',
          label: '积分发放adf价值',
          formatter(row) {
            return row.distributionADFTotalVale
              ? row.distributionADFTotalVale.toFixed(8) + ' ADF'
              : '-'
          }
        },
        {
          prop: 'recoveryPointsADFTotalVale',
          label: '积分回收消耗adf价值',
          formatter(row) {
            return row.recoveryPointsADFTotalVale
              ? row.recoveryPointsADFTotalVale.toFixed(8) + ' ADF'
              : '-'
          }
        }
      ]
    }
  },
  components: {
    createForm,
    createCard,
    createTable
  },
  created() {
    const { activeName } = this.$route.query
    if (activeName) {
      this.activeName = activeName
    }
    this.getPointsTotalValue(this.paramAssets)
    this.getPointsSettle(this.paramSettle)
  },
  methods: {
    //  导出积分资产
    dowloadAssets() {
      downloadAssets(this.paramAssets)
    },
    // 导出积分结算
    dowloadSettle() {
      downloadSettle(this.paramSettle)
    },
    // 监听表格表单
    changeSettle(obj) {
      const param = { ...this.paramSettle, ...obj }
      this.getPointsSettle(param)
    },
    // 监听表格变动
    changeAssets(obj) {
      const param = { ...this.paramAssets, ...obj }
      this.getPointsTotalValue(param)
    },
    // 积分资产搜索
    submitAssets(obj) {
      let param = this.formatParam(obj)
      this.paramAssets = { ...this.paramAssets, ...param }
      this.getPointsTotalValue(this.paramAssets)
    },
    // 积分结算搜索
    submetSettle(obj) {
      let param = this.formatParam(obj)
      this.paramSettle = { ...this.paramSettle, ...param }
      this.getPointsSettle(this.paramSettle)
    },
    // 格式化获取参数
    formatParam(param, start = 'startTime', end = 'endTime') {
      if (!param.date) {
        return {
          appletId: 1,
          startTime: Math.floor(
            (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
          ),
          endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1),
          ...param
        }
      }
      const obj = {}
      param[start] = Math.floor(+param.date[0] / 1000)
      param[end] = Math.floor(+param.date[1] / 1000) + 24 * 60 * 60 - 1
      for (let key in param) {
        if (
          (param[key] && key !== 'date') ||
          (param[key] === 0 && key !== 'date')
        ) {
          obj[key] = param[key]
        }
      }
      return obj
    },
    // 转换数组参数
    formatOptions(list) {
      if (!list) {
        return []
      }
      if (Array.isArray(list)) {
        return list.map((item, index) => ({ label: item, value: index }))
      } else {
        return Object.keys(list).map(item => ({
          label: list[item],
          value: item
        }))
      }
    },
    // 获取积分资产
    async getPointsTotalValue(param = {}) {
      const {
        totalCount,
        totalData = {},

        lists
      } = await getPointsTotalValue(param)
      this.tableDataAssets = lists
      this.cardDataAssets[0].value = totalData.distributionGBTTotalVale
        ? '¥' + (totalData.distributionGBTTotalVale / 100).toFixed(2)
        : '-'
      this.cardDataAssets[1].value = totalData.recoveryPointsGBTTotalVale
        ? '¥' + (totalData.recoveryPointsGBTTotalVale / 100).toFixed(2)
        : '-'
      this.cardDataAssets[2].value = totalData.distributionADFTotalVale
        ? 'ADF:' + totalData.distributionADFTotalVale.toFixed(8)
        : '-'
      this.cardDataAssets[3].value = totalData.recoveryPointsADFTotalVale
        ? 'ADF:' + totalData.recoveryPointsADFTotalVale.toFixed(8)
        : '-'
      this.totalAssets = totalCount

      // 后端返回搜索结果不稳定，先用本地
      // this.formConfigAssets[0].options = this.formatOptions(appletName)
    },
    // 获取积分结算数据
    async getPointsSettle(param = {}) {
      const {
        totalCount,
        totalData = {},

        lists
      } = await getPointsSettle(param)
      this.tableDataSettle = lists

      this.cardDataSettle[0].value = totalData.distributionPoints
      this.cardDataSettle[1].value = totalData.recoveryPoints
      this.cardDataSettle[2].value = totalData.unrecoveryPoints
      this.totalSettle = totalCount

      // 后端返回搜索结果不稳定，先用本地
      // this.formConfigSettle[0].options = this.formatOptions(appletName)
    }
  }
}
</script>

<style scoped lang="less">
.wrap {
  color: #333;
  background-color: white;
  padding: 20px;
  border-radius: 4px;
}
p {
  margin-bottom: 12px;
}
.form-inlne {
  float: right;
  display: flex;
}
</style>
