<template>
  <div class="turn-card-exchange-data-wrap">
    <div class="section-wrap">
      <div class="ops-wrap">
        <h1 class="title">翻牌数据</h1>
        <el-select @change="platformSelect"
          v-model="platformTypeValue"
          class="select-wrap"
          placeholder="请选择平台">
          <el-option value="" label="全部平台"></el-option>
          <el-option
            v-for="(item, index) in platformList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-date-picker
          class="picker-wrap"
          v-model="timeListValue"
          type="daterange"
          :clearable="false"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          :picker-options="pickerOptions"
          value-format="yyyyMMdd"
          @change="timeDataSelect">
        </el-date-picker>

        <el-button type="primary">查询</el-button>
      </div>

      <h2 class="seconde-title">活动数据</h2>
      <div class="digital-info-wrap">
        <div class="item">
          <p class="type-name">翻牌活动首页访问人数</p>
          <p class="type-num">{{detailInfo.cardIndexVisitorNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">翻牌人数</p>
          <p class="type-num">{{detailInfo.cardNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">翻牌次数</p>
          <p class="type-num">{{detailInfo.drawCount}}</p>
        </div>
        <div class="item">
          <p class="type-name">领取人数</p>
          <p class="type-num">{{detailInfo.fetchNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">领取张数</p>
          <p class="type-num">{{detailInfo.couponCount}}</p>
        </div>
      </div>
    </div>

    <div class="section-wrap">
      <div class="title-wrap">
        <h1 class="title">用户列表</h1>
        <el-button class="title-btn" type="primary" @click="exportData">导出</el-button>
      </div>
      <el-table class="table-wrap"
        header-row-class-name="table-header"
       :data="tableDataList">
        <el-table-column
          align="center"
          prop="unionId"
          label="UnionID">
        </el-table-column>
        <el-table-column
          align="center"
          prop="nickName"
          label="用户昵称">
        </el-table-column>
        <el-table-column
          align="center"
          label="平台">
          <template slot-scope="scope">
            {{platformList[scope.row.appletId]}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="drawCount"
          label="翻牌次数">
        </el-table-column>
        <el-table-column
          align="center"
          prop="couponCount"
          label="领取张数">
        </el-table-column>
        <el-table-column
          align="center"
          prop="collectCount"
          label="收藏数量">
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button @click="showDetail(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="(totalCount / pageSize)<= 1"
        :page-size="pageSize"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getTurnCardExchagneData, exportDataDefault } from '@/api/services/integralActiveManage'

export default {
  name: 'turn-card-exchange-data',
  components: {},
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      platformTypeValue: '', // 选择的平台类型，默认为空，表示全部
      platformList: {
        '1': '很实惠',
        '2': '聚驿优品'
      },
      isPost: false,
      detailInfo: {
        cardIndexVisitorNumber: 0, // 翻牌首页访问人数
        cardNumber: 0, // 翻牌人数
        drawCount: 0, // 翻牌次数
        fetchNumber: 0, // 领取人数
        couponCount: 0 // 领取张数
      },
      timeListValue: [],
      tableDataList: [],
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      page: 1
    }
  },
  created() {
    let _this = this

    _this.detailInfo = _this.$route.query
    _this.getListData()
  },
  methods: {
    getListData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getTurnCardExchagneData(params).then(res => {
          _this.detailInfo = {
            cardIndexVisitorNumber: res.cardIndexVisitorNumber,
            cardNumber: res.cardNumber,
            drawCount: res.drawCount,
            fetchNumber: res.fetchNumber,
            couponCount: res.couponCount
          }
          _this.tableDataList = res.list
          _this.totalCount = res.totalCount
          _this.pageSize = Number(res.pageSize)
          _this.isPost = false
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page,
          isExport: 1
        }

      exportDataDefault('turnCardExchangeData', params)
    },

    platformSelect() {
      this.getListData()
    },

    handleSizeChange(size) {
      this.pageSize = size
    },
    handleCurrentChange(page) {
      let _this = this

      _this.page = page
      _this.getListData()
    },

    timeDataSelect(time) {
      let _this = this

      _this.timeListValue = time
      _this.getListData()
    },

    showDetail(item) {
      this.$router.push({
        name: 'exchangeDataDetail',
        query: {
          nickName: item.nickName,
          unionId: item.unionId,
          appletId: this.platformTypeValue,
          platformType: this.platformList[this.platformTypeValue] || '全部'
        }
      })
    }
  }
}
</script>

<style lang="less">
</style>

<style lang="less" scoped>
.turn-card-exchange-data-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;
  }

  .ops-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #F2F3F7;
    margin-bottom: 16px;

    .title {
      flex: 1 0 90px;
      margin-right: 16px;
      margin-bottom: 12px;
      padding: 0;
      border: none;
    }
    .el-button {
      margin-bottom: 12px;
    }
  }

  .title-wrap {
    position: relative;
    border-bottom: 1px solid #F2F3F7;
    padding-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      padding-bottom: 0px;
      border-bottom: none;
    }
  }

  .title {
    padding-bottom: 12px;
    font-size: 20px;
    line-height: 28px;
    border-bottom: 1px solid #F2F3F7;
  }

  .seconde-title {
    margin-top: 16px;
    font-size: 16px;
    line-height: 20px;
  }

  .select-wrap {
    margin-bottom: 12px;
    width: 144px;
    margin-right: 16px;
  }

  .picker-wrap {
    margin-bottom: 12px;
    width: 300px;
    margin-right: 16px;
  }

  .digital-info-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .item {
      padding: 24px 16px 0px;
      display: flex;
      flex-direction: column;
      align-items: center;
      min-width: 150px;

      .type-name {
        font-size: 14px;
        color: #606166;
      }

      .type-num {
        font-size: 40px;
        line-height: 56px;
        color: #4D5AFF;
      }
    }
  }

  .table-wrap {
    margin-top: 24px;
  }
  .pagination-wrap {
    margin-top: 16px;
    text-align: right;
  }
}
</style>
