<template>
  <div class="home">
    <el-container class="home-wrap">
      <el-header style="height: 88px">
        <div class="logo">
          <span class="logo-title">积分管理后台</span>
        </div>

        <div class="header-nav">
          <div class="username">{{ userName }}</div>
          <router-link to="/login" class="layout">退出登录</router-link>
        </div>
      </el-header>

      <el-container class="home-inner">
        <el-aside width="215px">
          <el-menu class="home-aside-menu" :default-active="activeKey" @select="handlerMenuClick">
            <el-submenu v-for="(item1,index1) in asideList"
                :key="index1"
                :index="item1.key">
              <template slot="title"><i :class="item1.icon" v-if="item1.icon"></i>{{ item1.name }}</template>

              <span v-for="(item2, index2) in item1.children"
                  :key="index2"
                  :index="item2.key">
                <el-menu-item-group v-if="item2.children">
                  <template slot="title">{{item2.name}}</template>
                  <el-menu-item
                    :route="{name: item3.key}"
                    v-for="(item3, index3) in item2.children"
                    :key="index3"
                    :index="item3.key">
                    <i v-if="item3.icon"></i>{{ item3.name }}
                  </el-menu-item>
                </el-menu-item-group>

                <el-menu-item v-if="!item2.children" :index="item2.key" :route="{name: item2.key}">
                  <i v-if="item2.icon"></i>{{ item2.name }}
                </el-menu-item>

              </span>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main class="home-content">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      activeKey: 'dataProfile', // 默认显示的菜单key
      activeTab: '', // 默认打开的菜单
      asideList: [
        {
          name: '数据概览',
          icon: 'el-icon-tickets',
          key: 'data',
          children: [
            {
              name: '数据概览',
              key: 'totalDataProfile'
            },
            {
              name: '聚驿数据',
              key: 'juyiProfile'
            },
            {
              name: '很实惠数据',
              key: 'hshProfile'
            }
          ]
        }, {
          name: '平台用户管理',
          icon: 'el-icon-message',
          key: 'platformUserManage',
          children: [
            {
              name: '用户数据',
              key: 'platformUserData'
            },
            {
              name: '活跃数据',
              key: 'platformActiveData'
            }
          ]
        },
        {
          name: '资产管理',
          icon: 'el-icon-coin',
          key: 'IntegralIndex',
          children: [
            {
              name: '积分管理',
              key: 'IntegralManage'
            },
            {
              name: '积分资产',
              key: 'IntegralAsset'
            },
            {
              name: '积分购买',
              key: 'IntegralBuy'
            }
          ]
        }, {
          name: '爱兑分用户管理',
          icon: 'el-icon-setting',
          key: 'aiDuiFen',
          children: [
            {
              name: '用户数据',
              key: 'aiduifenUserData'
            }, {
              name: '活跃数据',
              key: 'aiduifenActiveData'
            }
          ]
        }, {
          name: '积分商城管理',
          icon: 'el-icon-goods',
          key: 'points-mall',
          children: [
            {
              name: '商品列表',
              key: 'pointsMallGoodList'
            }, {
              name: '拼多多商品列表',
              key: 'pointsMallPddGoodList'
            }, {
              name: '优惠券列表',
              key: 'pointsMallCouponList'
            }
          ]
        }, {
          name: '积分活动管理',
          icon: 'el-icon-s-home',
          key: 'integralActiveManage',
          children: [
            {
              name: '邀请好友送积分',
              key: 'inviteSendPoints'
            },
            {
              name: '翻牌换商品数据',
              key: 'turnCardExchangeData'
            }
          ]
        }
      ]
    }
  },
  computed: {
    userName() {
      return sessionStorage.getItem('userName') || this.$store.state.userName
    },

    changedAsideActiveName() {
      return this.$store.state.changedAsideActiveName
    }
  },
  watch: {
    changedAsideActiveName(val) {
      if (val) {
        this.activeKey = val
      }
    }
  },
  created() {
    let _this = this,
      route = _this.$route

    _this.activeKey = route.name
  },
  methods: {
    // 处理菜单点击事件
    handlerMenuClick(data) {
      this.$router.push({
        name: data
      })

      this.$store.dispatch('setChangedAsideActiveName', data)
    }
  }
}
</script>

<style lang="less">
.home {
  position: relative;
  background: #2D3340;

  .el-header,
  .el-aside,
  .el-main {
    box-sizing: border-box;
    color: #fff;
    background: #2D3340;
    height: 100%;
  }

  .home-wrap {
    height: 100vh;
  }

  .home-inner {
    display: flex;
  }

  .home-content {
    height: 100%;
  }

  .el-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 34px;

    .logo {
      display: flex;
      align-items: flex-end;

      .logo-title {
        margin-right: 8px;
        font-size: 28px;
      }

      .logo-sub-title {
        font-size: 18px;
      }
    }

    .header-nav {
      display: flex;
      align-items: center;
      line-height: 14px;

      .username {
        font-size: 14px;
      }

      .layout {
        color: #fff;
        font-size: 12px;
        text-decoration: none;
        margin-left: 8px;
      }
    }
  }

  .el-aside {
    padding: 0 16px;
    overflow: initial;
    // background: darkcyan;

    .home-aside-menu {
      border: none;
      background: #2D3340;

      .el-menu {
        background: #2D3340;
      }

      & > .el-menu-item,
      .el-submenu .el-submenu__title {
        padding: 0 16px !important;
      }
      // .home-aside-menu {
      // }

      .el-submenu .el-submenu__title,
      .el-menu-item {
        height: 40px;
        line-height: 40px;
        color: #fff;
        background: #2D3340;

        i {
          color: #fff;
        }
      }

      // .el-submenu .el-submenu__title {
      //   display: flex;
      //   align-items: center;
      //   justify-content: flex-start;
      // }

      .el-menu-item {
        box-sizing: border-box;
        min-width: 176px;
        border-radius: 4px;

        &.is-active {
          // background-color:
          background: #4D5AFF;
        }
      }

      .el-submenu {
        .el-submenu__title {
          display: flex;
          align-items: center;

          i {
            color: #fff;
          }
        }

        .el-menu-item {
          padding-left: 45px !important;
        }
      }
    }
  }
}
</style>
