<template>
  <div class="add-data">
    <div class="search">
      <el-select v-model="userOrigin" clearable placeholder="请选择用户来源" @change="userOriginChange">
        <el-option
        v-for="(item,index) in userOriginOptions"
        :key="index"
        :label="item"
        :value="index">
        </el-option>
      </el-select>

      <el-select v-model="userType" clearable placeholder="请选择用户类型" @change="userTypeChange">
        <el-option
        v-for="(item,index) in userTypeOptions"
        :key="index"
        :label="item"
        :value="index">
        </el-option>
      </el-select>
      <el-date-picker
        v-model="rangeDate"
        class="date-picker"
        type="daterange"
        @change="handleDateChange"
        align="left"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        value-format="yyyy-MM-dd">
      </el-date-picker>

      <el-input class="header-search"
        v-model="keyword"
        placeholder="请输入用户手机号、昵称进行搜索"
        clearable
        @clear="inputClear">
      </el-input>

      <el-button type="primary" @click="searchKey">搜索</el-button>
      <el-button type="success" class="export-btn" @click="exportData">导出</el-button>
    </div>
    <div class="num-box">
      <p>新增爱兑分用户</p>
      <p class="num">{{newUserTotal}}</p>
    </div>

    <p class="tab-title">用户列表（当天默认显示昨天数据）</p>
    <div class="table">
      <el-table :data="tableData" border>
        <el-table-column show-overflow-tooltip label="UnionId" prop="unionId"></el-table-column>
        <el-table-column show-overflow-tooltip label="用户昵称" prop="nickName"></el-table-column>
        <el-table-column show-overflow-tooltip label="手机号码" prop="phone"></el-table-column>
        <el-table-column show-overflow-tooltip label="授权登录时间" prop="authLoginTime"></el-table-column>
        <el-table-column show-overflow-tooltip label="昵称" prop="nickName"></el-table-column>
        <el-table-column show-overflow-tooltip label="注册时间" prop="regTime"></el-table-column>
        <el-table-column show-overflow-tooltip label="首次进入时间" prop="enterAppletTime"></el-table-column>
        <el-table-column show-overflow-tooltip label="推广渠道" prop="merchantLinkName"></el-table-column>
        <el-table-column show-overflow-tooltip label="用户类型" prop="userType"></el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination-wrap" v-if="parseInt(total) > 1">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="pageSize"
            layout="total, prev, pager, next, jumper"
            :total="parseInt(total)">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { newUsersApi } from '../../../api/services/aiduifen'
import urls from '@/config/urls'
import { formatTime } from '@/utils/index'

export default {
  data() {
    return {
      page: 1,
      pageSize: 20, // 每页显示多少条数据
      total: 0, // 总条数
      totalPage: 0, // 总页数
      activeName: 'addData',
      userOrigin: '',
      userOriginOptions: ['全部'],
      userType: '',
      userTypeOptions: ['未注册用户', '注册用户'],
      newUserTotal: 0, // 新增用户总数
      rangeDate: '',
      keyword: '',
      tableData: [] // 表格数据

    }
  },
  components: {
  },
  mounted() {
    let nowDate = new Date(),
      yesterday
    yesterday = formatTime(nowDate.setTime(nowDate.getTime() - 24 * 60 * 60 * 1000), 'yyyy-MM-dd')
    this.rangeDate = []
    this.rangeDate[0] = yesterday
    this.rangeDate[1] = yesterday
    this.getAddData(this.getQueryData())
  },
  methods: {
    // 获取新增数据列表

    getAddData(params) {
      let _this = this
      newUsersApi(params).then(res => {
        _this.tableData = res.list
        _this.newUserTotal = res.newUserTotal
        _this.total = Number(res.count)
      })
    },
    // 获取筛选数据
    getQueryData() {
      let time = this.rangeDate,
        params = {
          page: this.page,
          pageSize: this.pageSize,
          channelId: this.userOrigin === '' ? '' : this.userOrigin,
          isRegister: this.userType === '' ? undefined : this.userType,
          keyword: this.keyword === '' ? undefined : this.keyword
        }
      if (time) {
        params.startAt = `${time[0]} 00:00:00`
        params.endAt = `${time[1]} 23:59:59`
      }

      return params
    },
    // 选择用户来源
    userOriginChange(val) {
      this.userOrigin = val
      this.page = 1
      this.getAddData(this.getQueryData())
    },
    // 选择用户类型
    userTypeChange(val) {
      this.userType = val
      this.page = 1
      this.getAddData(this.getQueryData())
    },
    // 选择时间
    handleDateChange() {
      this.page = 1
      this.getAddData(this.getQueryData())
    },
    // 输入框清空
    inputClear() {
      this.keyword = ''
    },
    searchKey() {
      this.page = 1
      this.getAddData(this.getQueryData())
    },
    // 导出
    exportData() {
      let _this = this,
        _startAt = '',
        _endAt = '',
        downloadUrl = '',
        channelId = _this.userOrigin ? _this.userOrigin : '',
        isRegister = this.userType ? this.userType : '',
        keyword = this.keyword ? this.keyword : '',
        url = `${urls.backend}/wallet/users-data/export-new-users`
      if (this.rangeDate && this.rangeDate.length > 0) {
        _startAt = this.rangeDate[0] ? `${this.rangeDate[0]} 00:00:00` : ''
        _endAt = this.rangeDate[1] ? `${this.rangeDate[1]} 00:00:00` : ''
      }
      downloadUrl = `${url}?t=${sessionStorage.getItem('t')}&page=1&startAt=${_startAt}&endAt=${_endAt}&channelId=${channelId}&isRegister=${isRegister}&keyword=${keyword}`
      window.open(downloadUrl)
    },
    // 点击分页，获取数据
    handleCurrentChange(curPage) {
      this.page = curPage
      this.getAddData(this.getQueryData())
    }
  }
}
</script>
