<template>
  <div class="exchange-detail">
    <header>
      <p class="user-name">用户昵称：<span>{{userName}}</span></p>
      <el-button type="success" class="export export-btn" @click="exportData">导出</el-button>
    </header>

    <div class="table">
      <el-table :data="tableData" border>
        <el-table-column show-overflow-tooltip label="互换日期" prop="createdAt"></el-table-column>
        <el-table-column show-overflow-tooltip label="积分种类" prop="currencyTypeName"></el-table-column>
        <el-table-column show-overflow-tooltip label="互换方式" prop="modeType"></el-table-column>
        <el-table-column show-overflow-tooltip label="积分" prop="number"></el-table-column>
        <el-table-column show-overflow-tooltip label="互换用户" prop="platform"></el-table-column>
        <el-table-column show-overflow-tooltip label="ADF" prop="adfAmount"></el-table-column>
        <el-table-column show-overflow-tooltip label="类型" prop="typeText"></el-table-column>
        <el-table-column show-overflow-tooltip label="状态" prop="statusText"></el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination-wrap" v-if="parseInt(total) > 1">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="pageSize"
            layout="total, prev, pager, next, jumper"
            :total="parseInt(total)">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { exchangeDetail } from '../../api/services/aiduifen'
import urls from '@/config/urls'

export default {
  data() {
    return {
      page: 0,
      pageSize: 20, // 每页显示多少条数据
      total: 0, // 总条数
      totalPage: 0, // 总页数
      uid: '',
      tableData: [], // 表格数据
      userName: '' // 用户名称
    }
  },
  mounted() {
    this.uid = this.$route.query.id
    this.userName = this.$route.query.name
    this.getData(this.getQueryData())
  },
  methods: {
    // 获取新增数据列表
    getData(params) {
      let _this = this
      exchangeDetail(params).then(res => {
        _this.tableData = res.list
        _this.activeExchangeTotal = res.totalCount
        _this.total = Number(res.totalCount)
      })
    },
    // 获取筛选数据
    getQueryData() {
      let
        params = {
          page: this.page,
          pageSize: this.pageSize,
          uid: this.uid ? this.uid : ''
        }
      return params
    },
    // 导出
    exportData() {
      let _startAt = '',
        _endAt = '',
        downloadUrl = '',
        searchKey = this.keyword ? this.keyword : '',
        uid = this.uid,
        url = `${urls.backend}/wallet/active-data/exchange-detailed`
      if (this.rangeDate && this.rangeDate.length > 0) {
        _startAt = this.rangeDate[0] ? `${this.rangeDate[0]} 00:00:00` : ''
        _endAt = this.rangeDate[1] ? `${this.rangeDate[1]} 23:59:59` : ''
      }
      downloadUrl = `${url}?t=${sessionStorage.getItem('t')}&page=0&startAt=${_startAt}&endAt=${_endAt}&searchKey=${searchKey}&uid=${uid}&isExport=1`
      console.log('33--', downloadUrl)
      window.open(downloadUrl)
    },
    // 点击分页，获取数据
    handleCurrentChange(curPage) {
      this.page = curPage - 1
      this.getData(this.getQueryData())
    }
  }
}
</script>
<style lang="less">
.exchange-detail {
  height: 95%;
  background: #fff;
  border-radius: 20px;
  padding: 15px;
  color: #333;
  overflow-y: auto;
  header {
    height: 50px;
  }
  .user-name {
    margin-bottom: 20px;
    float: left;
  }
  .export {
    float: right;
  }
}
</style>
