<template>
  <div class="platform-user-data-wrap">
    <div class="section-wrap">
      <div class="ops-wrap inline-wrap">
        <h1 class="title">新增数据</h1>
        <el-select @change="platformSelect"
          v-model="platformTypeValue"
          class="select-wrap first-select"
          placeholder="请选择平台">
          <el-option value="" label="全部平台"></el-option>
          <el-option
            v-for="(item, index) in platformList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-select @change="timeTypeSelect"
          v-model="timeTypeValue"
          class="select-wrap"
          placeholder="请选择筛选时间">
          <el-option value="" label="全部"></el-option>
          <el-option
            v-for="(item, index) in timeTypeList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-select @change="regTypeSelect"
          v-model="regTypeValue"
          class="select-wrap"
          placeholder="请选择用户来源">
          <el-option value="" label="全部用户来源"></el-option>
          <el-option
            v-for="(item, index) in regTypeList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-date-picker
          :disabled="timeTypeValue==''"
          class="picker-wrap"
          v-model="timeListValue"
          type="daterange"
          :clearable="false"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          :picker-options="pickerOptions"
          value-format="yyyyMMdd"
          @change="timeDataSelect">
        </el-date-picker>
        <el-input class="input-wrap"
          placeholder="请输入用户手机号、昵称进行搜索"
          prefix-icon="el-icon-search"
          v-model="inputSearchValue">
        </el-input>

        <el-button class="btn" type="primary" @click="getListData">查询</el-button>
        <el-button class="btn" type="primary" @click="exportData">导出</el-button>
      </div>

      <div class="user-num-wrap">
        <h2 class="num-title">新增用户数</h2>
        <p>{{totalCount}}</p>
      </div>
    </div>

    <div class="section-wrap">
      <h1 class="title">用户列表(当天默认显示昨日数据)</h1>
      <el-table class="table-wrap"
        header-row-class-name="table-header"
       :data="tableDataList">
        <el-table-column
          align="center"
          prop="unionId"
          label="UnionID">
        </el-table-column>
        <el-table-column
          align="center"
          prop="nickName"
          label="用户昵称">
        </el-table-column>
        <el-table-column
          align="center"
          prop="phone"
          label="手机号码">
        </el-table-column>
        <el-table-column
          align="center"
          label="平台">
          <template slot-scope="scope">
            {{platformList[scope.row.appletId]}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="用户类型">
          <template slot-scope="scope">
            {{scope.row.phone ? '已注册' : '未注册'}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="授权登录时间">
          <template slot-scope="scope">
            {{scope.row.regTime && formatTime(scope.row.regTime)}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="注册时间">
          <template slot-scope="scope">
            {{scope.row.getPhoneTime && formatTime(scope.row.getPhoneTime)}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="首次进入平台时间">
          <template slot-scope="scope">
            {{scope.row.enterAppletTime && formatTime(scope.row.enterAppletTime)}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="用户来源">
          <template slot-scope="scope">
            {{regTypeList[String(scope.row.source)]}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="操作">
          <template slot-scope="scope">
            <el-button @click="showUserDetail(scope.row)">查看明细</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="totalPage <= 1"
        :current-page="page"
        :page-size="pageSize"
        :page-count="totalPage"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPlatformUserManageListData, exportUserManageData } from '@/api/services/platformUserManage'
import { formatTime } from '@/utils'

export default {
  name: 'platform-user-data',
  components: {},
  data() {
    return {
      isPost: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      platformTypeValue: '',
      platformList: {},
      timeTypeValue: '2', // 选择的时间类型，默认为空，表示全部
      timeTypeList: {},
      regTypeValue: '', // 选择的用户来源，默认为空，表示全部
      regTypeList: {},
      timeListValue: [], // 选择的时间段
      inputSearchValue: '',
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      totalPage: 1, // 总页数
      page: 0,
      tableDataList: [
        // {
        //   nickName: 'test002',
        //   unionId: 'test000001001000test2',
        //   appletId: '2',
        //   source: 1,
        //   regTime: 1563696886
        // }
      ] // 数据列表数据来源
    }
  },
  created() {
    let _this = this,
      // end = new Date(), // 设置日期为当前日期
      start = new Date(new Date().setDate(new Date().getDate() - 1)), // 设置日期为当前日期前七天
      formattedTime = formatTime(start, 'yyyyMMdd')

    _this.timeListValue = [formattedTime, formattedTime]
    _this.getListData()
  },
  methods: {
    getListData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          timeType: _this.timeTypeValue,
          registerType: _this.regTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          keyword: _this.inputSearchValue,
          pageSize: _this.pageSize,
          page: _this.page
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getPlatformUserManageListData(params).then(res => {
          _this.isPost = false
          _this.platformList = res.appletList
          _this.timeTypeList = res.timeType
          _this.regTypeList = res.registerType
          _this.tableDataList = res.userList.list

          _this.page = Number(res.userList.page)
          _this.totalCount = res.userList.totalCount
          _this.pageSize = Number(res.userList.pageSize)
          _this.totalPage = res.userList.totalPage
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          timeType: _this.timeTypeValue,
          registerType: _this.regTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          keyword: _this.inputSearchValue,
          pageSize: _this.pageSize,
          page: _this.page
        }

      exportUserManageData(params)
    },

    platformSelect() {
      this.getListData()
    },

    timeTypeSelect(value) {
      if (!value) {
        this.timeListValue = []
      }
      this.getListData()
    },

    regTypeSelect() {
      this.getListData()
    },

    formatTime(time) {
      return formatTime(time * 1000, 'yyyy-MM-dd hh:mm:ss')
    },

    handleSizeChange(size) {
      this.pageSize = size
    },

    handleCurrentChange(page) {
      let _this = this

      _this.page = page - 1
      _this.getListData()
    },

    timeDataSelect(time) {
      let _this = this

      _this.timeListValue = time
      _this.getListData()
    },

    showUserDetail(item) {
      let _this = this,
        _item = JSON.parse(JSON.stringify(item))

      _item.paltformType = _this.platformList[_item.appletId] || ''
      _item.registerTime = formatTime(_item.getPhoneTime) || ''

      sessionStorage.setItem('userManageDetail', JSON.stringify(_item))
      _this.$router.push({
        name: 'platformUserDataDetail'
      })
    }
  }
}
</script>

<style lang="less">
// .platform-user-data-wrap {
//   .el-range-input,
//   .el-input__inner {
//     width: 120px;
//   }
// }
</style>

<style lang="less" scoped>
.platform-user-data-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;
  }

  .inline-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 1px solid #F2F3F7;
    margin-bottom: 16px;

    .title {
      flex: 1 0 100px;
      margin-bottom: 12px;
      padding: 0;
      border: none;
    }
  }

  .title {
    padding-bottom: 12px;
    font-size: 20px;
    line-height: 28px;
    border-bottom: 1px solid #F2F3F7;
  }

  .select-wrap {
    margin-bottom: 12px;
    width: 120px;
    margin-right: 16px;

    &.first-select {
      width: 100px;
    }
  }

  .picker-wrap {
    margin-bottom: 12px;
    width: 235px;
    margin-right: 16px;
  }

  .input-wrap {
    margin-bottom: 12px;
    width: 230px;
    margin-right: 16px;
  }

  .btn {
    margin-bottom: 12px;
  }

  .user-num-wrap {
    max-width: 300px;
    margin: 0 auto;
    padding: 24px 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .num-title {
      font-size: 14px;
      color: #606166;
    }

    p {
      padding-top: 12px;
      font-size: 40px;
      color: #4D5AFF;
    }
  }

  .pagination-wrap {
    margin-top: 16px;
    text-align: right;
  }
}
</style>
