<template>
  <div class="profile-data" v-loading="loading">
    <div class="group grown-data">
      <div class="group-head">
        <div class="group-title">
          <h1>流失数据</h1>
          <span class="tips">(当天默认显示昨天数据)</span>
        </div>

        <div class="screen">
          <el-date-picker
            v-model="time"
            :clearable="false"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions">
          </el-date-picker>

          <el-button type="primary" @click="getLostCustomeData">查询</el-button>
        </div>
      </div>

      <div class="group-content">
        <div class="grown-data-list">
          <div class="grown-data-item">
            <div class="title">客户有效资产流失数据图</div>
            <LineChart :dimensions="dimensions" :data="chartData"></LineChart>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dateOptionsMixins from '@/components/dateOptionsMixins'
import LineChart from '@/components/echartComponents/LineChart'
import chartData from './chartData'
import { getLostCustomerApi } from '@/api/services/dataProfile'
import { formatTime } from '@/utils/index'

export default {
  name: 'lostData',
  mixins: [dateOptionsMixins, chartData],
  components: {
    LineChart
  },
  data() {
    let nowDate = Date.now(),
      oneDay = 86400 * 1000 // 一天时间毫秒数

    return {
      loading: false, // 加载loading
      time: [nowDate - oneDay * 8, nowDate - oneDay], // 日期筛选
      dimensions: ['product', '新增有效客户数量', '流失有效客户数量'],
      chartData: {}, // 图表数据
      originData: {} // 源数据
    }
  },
  mounted() {
    this.getLostCustomeData()
  },
  methods: {
    random(lower, upper) {
      return Math.floor(Math.random() * (upper - lower + 1)) + lower
    },

    // 获取流失数据
    getLostCustomeData() {
      let time = this.time,
        params = {}

      if (time) {
        params.startAt = formatTime(time[0], 'yyyyMMdd')
        params.endAt = formatTime(time[1], 'yyyyMMdd')
      }

      this.loading = true

      getLostCustomerApi(params).then(res => {
        this.originData = res
        this.handlerDate()
        this.loading = false
      })
    },

    // 处理返回数据
    handlerDate() {
      let _this = this

      _this.chartData = _this.handelerDayData(_this.time, _this.handlerChartData)
    },

    // 处理天数据
    handlerChartData(time) {
      let currentData = this.originData[formatTime(new Date(time), 'yyyyMMdd')],
        lost = 0, // 流失客户数
        effective = 0 // 有效客户数

      if (currentData) {
        lost = currentData.lost || 0
        effective = currentData.effective || 0
      }

      return [effective, lost]
    }
  }
}
</script>

<style lang="less" scoped>
.profile-data {
  .group {
    border-radius: 8px;
    margin: 0 16px;

    & + .group {
      margin-top: 16px;
    }

    .group-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 24px 16px;
      background: #fff;
      border-radius: 8px;
      margin-bottom: 16px;

      .group-title {
        display: flex;
        align-items: flex-end;
        color: #333;

        h1 {
          margin-right: 8px;
          font-size: 20px;
        }

        .tips {
          font-size: 12px;
        }
      }

      .screen {
        .el-button {
          margin-left: 16px;
        }
      }
    }

    &.grown-data {
      .grown-data-list {
        display: flex;

        & + .grown-data-list {
          margin-top: 16px;
        }

        .grown-data-item {
          width: 100%;
          background: #fff;
          border-radius: 8px;

          .title {
            padding: 24px 16px;
            color: #333;
            font-size: 16px;
          }
        }
      }
    }
  }
}
</style>
