<template>
  <div class="card_title_container">
    <h3 class="title">{{title}}</h3>
    <div class="fl">
      <slot></slot>
      <Search-time v-if="type === 'search'" @search="search"></Search-time>
      <el-button type="primary" :style="buttonStyle" v-if="type === 'export'" @click="exportData">导 出</el-button>
      <slot name="after"></slot>
    </div>
  </div>
</template>

<script>
import SearchTime from './SearchTime'

export default {
  name: 'TitleItem',
  props: ['type', 'title', 'buttonStyle'],
  components: { SearchTime },
  data() {
    return {}
  },
  methods: {
    search(val) {
      this.$emit('handleData', val)
    },
    exportData() {
      this.$emit('handleData')
    }
  }
}
</script>

<style lang='less' scoped>
  .card_title_container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .title {
      color: #333333;
      font-family: PingFangSC-Regular;
      font-size: 20px;
      font-weight: normal;
    }

    .fl {
      display: flex;
    }
  }
</style>
