<template>
  <div class="data-profile">
    <el-tabs v-model="activeTabName" @tab-click="handleClick">
      <el-tab-pane label="数据概况" name="profile"></el-tab-pane>
      <el-tab-pane label="活跃数据" name="active"></el-tab-pane>
      <el-tab-pane label="资产数据" name="asset"></el-tab-pane>
      <el-tab-pane label="流失数据" name="lost"></el-tab-pane>
    </el-tabs>

    <profileData v-if="activeTabName === 'profile'"></profileData>
    <activeData v-if="activeTabName === 'active'"></activeData>
    <assetData v-if="activeTabName === 'asset'"></assetData>
    <lostData v-if="activeTabName === 'lost'"></lostData>
  </div>
</template>

<script>
import profileData from './components/dataProfile' // 数据概览
import assetData from './components/assetData' // 资产数据
import activeData from './components/activeData' // 活跃数据
import lostData from './components/lostData' // 流失数据

export default {
  name: 'dataProfile',
  data() {
    return {
      activeTabName: 'profile'
    }
  },
  components: {
    profileData,
    assetData,
    activeData,
    lostData
  },
  methods: {
    // 处理tab点击事件
    handleClick(tab, event) {
      this.activeTabName = tab.name
    }
  }
}
</script>

<style lang="less">
.data-profile {
  .el-tabs {
    padding: 0 32px;
    margin-bottom: 16px;
    background: #fff;

    .el-tabs__header {
      margin: 0;
    }

    .el-tabs__item {
      height: 56px;
      line-height: 56px;
      font-size: 16px;

      &.is-active {
        font-weight: bold;
      }
    }
  }
}
</style>
