import request from '../request'
import urls from '@/config/urls'
import qs from 'qs'

// 获取用户数据
export function getPlatformUserManageListData(data) {
  return request({
    url: '/user/user/query-user-list',
    params: data
  })
}

// 导出数据
export function exportUserManageData(data) {
  data.t = sessionStorage.getItem('t') || ''

  window.open(`${urls.backend}/user/user/export-user-list?${qs.stringify(data)}`, '_blank')
}

// 获取详细数据
export function getPlatformUserDetail(data) {
  return request({
    url: '/user/user/query-user-detail',
    params: data
  })
}

// 获取用户活跃数据
export function getPlatformActiveData(data) {
  return request({
    url: '/user/user/query-effective-user-list',
    params: data
  })
}

// 获取积分用户活跃数据
export function getPlatformDigitalActiveData(data) {
  return request({
    url: '/user/user/query-point-effective-user-list',
    params: data
  })
}

// 获取积分用户详情数据
export function getActiveDetailData(data) {
  return request({
    url: '/user/user/query-user-point-detail',
    params: data
  })
}

// 导出数据
export function exportDataDefault(type, data) {
  let url = ''

  data.t = sessionStorage.getItem('t') || ''
  switch (type) {
  case 'platformActiveData':
    url = `${urls.backend}/user/user/query-effective-user-list?`
    break
  case 'platformDigitalActiveData':
    url = `${urls.backend}/user/user/query-point-effective-user-list?`
    break
  case 'platformUserDataDetail':
    url = `${urls.backend}/user/user/query-user-point-detail?`
    break
  default:
    throw new Error('exportDataDefault需要配置导出类型')
  }

  window.open(`${url}${qs.stringify(data)}`, '_blank')
}
