import request from '../request'
import urls from '@/config/urls'
import qs from 'qs'

// 获取邀请送积分数据
export function getInviteSendPointsData(data) {
  return request({
    url: '/user/user/golden-data',
    params: data
  })
}

// 获取邀请送积分详情数据
export function getInviteSendPointsDetailData(data) {
  return request({
    url: '/user/user/invite-user-data',
    params: data
  })
}

// 获取翻牌换商品数据
export function getTurnCardExchagneData(data) {
  return request({
    url: '/user/user/card-data',
    params: data
  })
}

// 获取翻牌换商品详情数据
export function getTurnCardExchagneDetail(data) {
  return request({
    url: '/user/user/query-coupon-draw',
    params: data
  })
}

// 导出数据

export function exportDataDefault(type, data) {
  let url = ''

  data.t = sessionStorage.getItem('t') || ''
  switch (type) {
  case 'inviteSendPoints':
    url = `${urls.backend}/user/user/golden-data?`
    break
  case 'inviteSendDetail':
    url = `${urls.backend}/user/user/invite-user-data?`
    break
  case 'turnCardExchangeData':
    url = `${urls.backend}/user/user/card-data?`
    break
  case 'exchangeDataDetail':
    url = `${urls.backend}/user/user/query-coupon-draw?`
    break
  default:
    throw new Error('exportDataDefault需要配置导出类型')
  }

  window.open(`${url}${qs.stringify(data)}`, '_blank')
}
