<template>
  <div class="wrap">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <h1>现金购买数据</h1>
      </div>
      <div class="form-inlne">
        <create-form :formConfig="formConfig" @submit="submit"></create-form>
        <el-button type="primary" style="margin-left:20px" @click="dowload">导出</el-button>
      </div>
      <create-card :data="cardData"></create-card>
      <p>用户列表（默认显示昨天数据）</p>
      <create-table
        :tableData="tableData"
        :tableConfig="tableConfig"
        :totalCount="totalCount"
        @change="change"
      ></create-table>
    </el-card>
  </div>
</template>

<script>
import createForm from '../common/CreateForm'
import createCard from '../common/CreateCard'
import createTable from '../common/CreateTable'
import { getBuy, downloadBuy } from '../../../api/services/assetsManage'

export default {
  data() {
    return {
      totalCount: 0,
      param: {
        startTime: Math.floor(
          (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
        ),
        endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1)
      },
      // 表单配置项
      formConfig: [
        {
          type: 'select',
          key: 'appletId',
          options: [{ label: '很实惠', value: 1 }, { label: '聚驿', value: 2 }]
        },
        {
          type: 'select',
          key: 'dateKey',
          placeholder: '请选择时间类型',
          options: [
            { label: '统计时间', value: 1 },
            {
              label: '最近购买时间',
              value: 2
            }
          ]
        },
        {
          type: 'datePicker',
          key: 'date',
          value: [
            new Date(+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60),
            new Date(Math.floor(+new Date().setHours(0, 0, 0, 0)) - 1)
          ]
        }
      ],
      cardData: [{ label: '现金购买商品金额', value: 0 }],
      tableData: [],
      // 表格配置项
      tableConfig: [
        { prop: 'unionId', label: 'unionId' },
        { prop: 'nickname', label: '用户昵称' },
        {
          prop: 'appletId',
          label: '平台',
          formatter(row) {
            return row.appletId == 2 ? '聚驿' : '很实惠'
          }
        },
        {
          prop: 'totalPayment',
          label: '支付总金额',
          formatter(row) {
            return (row.totalPayment / 100).toFixed(2)
          }
        },
        {
          prop: 'settleAmount',
          label: '返现总金额',
          formatter(row) {
            return (row.settleAmount / 100).toFixed(2)
          }
        },
        {
          prop: 'buyTimes',
          label: '消费次数'
        }
      ]
    }
  },
  components: { createForm, createCard, createTable },
  created() {
    this.getBuy(this.param)
  },
  methods: {
    // 导出
    dowload() {
      downloadBuy(this.param)
    },
    // 监听表格参数变动
    change(obj) {
      const param = { ...this.param, ...obj }
      this.getBuy(param)
    },
    // 搜索
    submit(obj) {
      this.param = { ...this.param, ...this.formatParam(obj) }
      this.getBuy(this.param)
    },
    // 参数对象转数组
    formatOptions(list) {
      if (!list) {
        return []
      }
      if (Array.isArray(list)) {
        return list.map((item, index) => ({ label: item, value: index }))
      } else {
        return Object.keys(list).map(item => ({
          label: list[item],
          value: item
        }))
      }
    },
    // 格式化获取参数
    formatParam(param, start = 'startTime', end = 'endTime') {
      if (!param.date) {
        return {
          startTime: Math.floor(
            (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
          ),
          endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1),
          ...param
        }
      }
      const obj = {}
      param[start] = Math.floor(+param.date[0] / 1000)
      param[end] = Math.floor(+param.date[1] / 1000) + 24 * 60 * 60 - 1
      for (let key in param) {
        if (
          (param[key] && key !== 'date') ||
          (param[key] === 0 && key !== 'date')
        ) {
          obj[key] = param[key]
        }
      }
      return obj
    },
    // 获取现金购买数据
    async getBuy(param = {}) {
      const { totalPayment, lists, appletName, totalCount } = await getBuy(
        param
      )
      this.cardData[0].value = totalPayment
        ? '¥' + (totalPayment / 100).toFixed(2)
        : '-'
      this.tableData = lists
      this.totalCount = totalCount
      if (!appletName) {
        return
      }
      this.formConfig[0].options = this.formatOptions(appletName)
    }
  }
}
</script>

<style scoped lang="less">
p {
  margin-bottom: 12px;
}
.wrap {
  color: #333;
}
.form-inlne {
  display: flex;
  float: right;
}
.box-card {
  padding-bottom: 20px;
}
</style>
