<template>
  <div class="data_content">
    <div v-for="(item, index) in totalList" :key="index" @click="routerTo(item)" :class="{'cus': item.path}">
      <p class="total_item_label">{{item.label}}</p>
      <p class="total_item_val">{{item.value || 0}}</p>
      <slot :scope="item"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ['totalList'],
  name: 'TotalData',
  methods: {
    routerTo({ path }) {
      if (path) this.$router.push(path)
    }
  }
}
</script>

<style scoped lang="less">
  .data_content {
    display: flex;
    justify-content: center;
    margin-top: 48px;

    > div {
      flex: 1;
      text-align: center;

      .total_item_label {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #606166;
      }

      .total_item_val {
        font-family: PingFangSC-Regular;
        font-size: 40px;
        color: #4D5AFF;
        margin-top: 16px;
        margin-bottom: 8px;
      }
    }
  }
</style>
