<template>
  <div class="body">
    <el-tabs v-model="activeName">
      <el-tab-pane label="获取积分数据" name="getPoint">
        <div>
          <div class="search">
            <create-form :formConfig="formConfigGet" @submit="submitGet"></create-form>
            <el-button type="primary" style="margin-left:20px" @click="exportGet">导出</el-button>
          </div>
          <create-card :data="cardDataGet" />
          <p style="margin-bottom:12px">用户列表（当日默认显示昨日数据）</p>
          <create-table
            :tableData="tableDataGet"
            :tableConfig="tableConfigGet"
            @change="changeGet"
            :totalCount="totalCountGet"
          ></create-table>
        </div>
      </el-tab-pane>
      <el-tab-pane label="积分换购数据" name="exchangePoint">
        <div class="search">
          <create-form :formConfig="formConfigPost" @submit="submitPost"></create-form>
          <el-button type="primary" style="margin-left:20px" @click="exportPost">导出</el-button>
        </div>
        <create-card :data="cardDataPost" />
        <p style="margin-bottom:12px">用户列表（当日默认显示昨日数据）</p>
        <create-table
          :totalCount="totalCountPost"
          :tableData="tableDataPost"
          :tableConfig="tableConfigPost"
          @change="changePost"
        ></create-table>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import createTable from '../common/CreateTable'
import createForm from '../common/CreateForm'
import createCard from '../common/CreateCard'
import {
  getPoints,
  downloadPost,
  postPoints,
  downloadGet
} from '../../../api/services/assetsManage'
import { formatTime } from '../../../utils'
export default {
  data() {
    return {
      activeName: 'getPoint',
      // 积分获取数据
      paramGet: {
        startTime: Math.floor(
          (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
        ),
        endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1)
      },
      totalCountGet: 0,
      cardDataGet: [{ label: '首次获取积分用户', value: 0 }],
      // 积分获取表单配置项
      formConfigGet: [
        {
          type: 'select',
          key: 'appletId',
          options: [{ label: '很实惠', value: 1 }, { label: '聚驿', value: 2 }]
        },
        {
          type: 'datePicker',
          key: 'date',
          value: [
            new Date(+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60),
            new Date(Math.floor(+new Date().setHours(0, 0, 0, 0)) - 1)
          ]
        },
        {
          type: 'input',
          key: 'nicknameOrPhone',
          placeholder: '请输入用户手机号，昵称进行搜索'
        }
      ],
      tableDataGet: [],
      // 积分获取表格配置项
      tableConfigGet: [
        {
          prop: 'receiveTime',
          label: '首次获取时间',
          formatter(row) {
            return formatTime(row.receiveTime * 1000)
          }
        },
        { prop: 'unionId', label: 'unionId' },
        { prop: 'nickname', label: '用户昵称' },
        {
          prop: 'appletId',
          label: '平台',
          formatter(row) {
            return row.appletId == 2 ? '聚驿' : '很实惠'
          }
        },
        {
          prop: 'points',
          label: '积分',
          formatter(row) {
            return '+' + row.points
          }
        }
      ],
      // 积分换购数据
      totalCountPost: 0,
      // 积分兑换表单配置项
      formConfigPost: [
        {
          type: 'select',
          key: 'appletId',
          options: [{ label: '很实惠', value: 1 }, { label: '聚驿', value: 2 }]
        },
        {
          type: 'select',
          key: 'buyType',
          placeholder: '请选择积分类型',
          options: [
            { label: '积分', value: 1 },
            { label: '积分+现金', value: 2 }
          ]
        },
        {
          type: 'datePicker',
          key: 'date',
          value: [
            new Date(+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60),
            new Date(Math.floor(+new Date().setHours(0, 0, 0, 0)) - 1)
          ]
        },
        {
          type: 'input',
          key: 'keyword',
          placeholder: '请输入用户手机号，昵称进行搜索'
        }
      ],
      cardDataPost: [
        { label: '首次积分兑换用户', value: '0' },
        { label: '首次积分+现金购买用户', value: '0' }
      ],
      paramPost: {
        startAt: formatTime(
          Math.floor(+new Date() - 1000 * 48 * 60 * 60),
          'yyyyMMdd'
        ),
        endAt: formatTime(
          Math.floor(+new Date() - 1000 * 24 * 60 * 60),
          'yyyyMMdd'
        )
      },
      tableDataPost: [],
      // 积分兑换表格配置项
      tableConfigPost: [
        { prop: 'date', label: '兑换购买日期' },
        { prop: 'unionId', label: 'unionId' },
        { prop: 'nickName', label: '用户昵称' },
        {
          prop: 'appletName',
          label: '平台'
        },
        { prop: 'actionTypeName', label: '商品类型' },
        { prop: 'goodsName', label: '商品名称' },
        { prop: 'buyTypeName', label: '类型' },
        {
          prop: 'payment',
          label: '支付金额',
          formatter(row) {
            return (row.payment / 100).toFixed(2)
          }
        },
        {
          prop: 'costPoints',
          label: '消耗积分',
          formatter(row) {
            return -row.costPoints
          }
        }
      ]
    }
  },
  components: {
    createTable,
    createForm,
    createCard
  },
  created() {
    const { activeName } = this.$route.query
    if (activeName) {
      this.activeName = activeName
    }
    this.getPoints(this.paramGet)
    this.postPoints(this.paramPost)
  },
  methods: {
    // 导出积分结算
    exportGet() {
      downloadGet(this.paramGet)
    },
    // 获取积分兑换数据
    async postPoints(param = {}) {
      const {
        list,
        buyType,
        appletName,
        totalCount,
        summary
      } = await postPoints(param)
      this.tableDataPost = list
      this.cardDataPost[0].value = summary.firstPayByPointsUnionIdCount
      this.cardDataPost[1].value = summary.firstPayByPointsAndMoneyUnionIdCount
      this.formConfigPost[0].options = this.formatOptions(appletName)
      this.formConfigPost[1].options = this.formatOptions(buyType)
      this.totalCountPost = totalCount
    },
    // 获取积分结算数据
    async getPoints(param = {}) {
      const { totalCount, totalUser, lists, appletName } = await getPoints(
        param
      )
      this.cardDataGet[0].value = totalUser
      this.formConfigGet[0].options = this.formatOptions(appletName)
      this.tableDataGet = lists
      this.totalCountGet = totalCount
    },

    // 转换数组参数
    formatOptions(list) {
      if (!list) {
        return []
      }
      if (Array.isArray(list)) {
        return list.map((item, index) => ({ label: item, value: index }))
      } else {
        return Object.keys(list).map(item => ({
          label: list[item],
          value: item
        }))
      }
    },
    // 表格参数变动监听
    changePost(param) {
      param = { ...param, ...this.paramPost }
      this.postPoints(param)
    },
    changeGet(param) {
      param = { ...param, ...this.paramGet }
      this.getPoints(param)
    },
    // 格式化获取参数
    formatParam(param, start = 'startAt', end = 'endAt') {
      const obj = {}
      if (start == 'startTime') {
        if (!param.date) {
          return {
            startTime: Math.floor(
              (+new Date().setHours(0, 0, 0, 0) - 1000 * 24 * 60 * 60) / 1000
            ),
            endTime: Math.floor(+new Date().setHours(0, 0, 0, 0) / 1000 - 1),
            ...param
          }
        }
        param[start] = Math.floor(+param.date[0] / 1000)
        param[end] = Math.floor(+param.date[1] / 1000) + 24 * 60 * 60 - 1
      } else {
        if (!param.date) {
          return param
        }
        param[start] = formatTime(param.date[0], 'yyyyMMdd')
        param[end] = formatTime(param.date[1], 'yyyyMMdd')
      }

      for (let key in param) {
        if (
          (param[key] && key !== 'date') ||
          (param[key] === 0 && key !== 'date')
        ) {
          obj[key] = param[key]
        }
      }
      return obj
    },
    // 搜索
    submitGet(param) {
      this.paramGet = {
        ...this.paramGet,
        ...this.formatParam(param, 'startTime', 'endTime')
      }
      this.getPoints(this.paramGet)
    },
    submitPost(param) {
      this.paramPost = this.formatParam(param)
      this.postPoints(this.paramPost)
    },
    // 导出积分兑换
    exportPost() {
      downloadPost(this.paramPost)
    }
  }
}
</script>

<style scoped lang="less">
.search {
  display: flex;
  float: right;
}
h2 {
  margin-top: 20px;
  font-size: 24px;
}
.picker {
  margin: 0 16px;
}
.body {
  color: #333;
  background-color: white;
  padding: 20px;
  border-radius: 4px;
}
.input {
  width: 250px;
}
</style>
