<template>
  <div class="invite-send-points-wrap">
    <div class="section-wrap">
      <div class="ops-wrap">
        <h1 class="title">邀请送积分</h1>
        <el-select @change="platformSelect"
          v-model="platformTypeValue"
          class="select-wrap"
          placeholder="请选择平台">
          <el-option value="" label="全部平台"></el-option>
          <el-option
            v-for="(item, index) in platformList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <el-date-picker
          class="picker-wrap"
          v-model="timeListValue"
          type="daterange"
          :clearable="false"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          :picker-options="pickerOptions"
          value-format="yyyyMMdd"
          @change="timeDataSelect">
        </el-date-picker>
        <el-button type="primary">查询</el-button>
      </div>

      <h2 class="seconde-title">活动数据</h2>
      <div class="digital-info-wrap">
        <div class="item">
          <p class="type-name">邀请活动首页访问人数</p>
          <p class="type-num">{{detailInfo.visitorNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">邀请人数</p>
          <p class="type-num">{{detailInfo.inviteNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">邀请金豆奖励</p>
          <p class="type-num">{{detailInfo.inviteAward}}</p>
        </div>
      </div>

      <div class="digital-info-wrap">
        <div class="item">
          <p class="type-name">领取积分人数</p>
          <p class="type-num">{{detailInfo.fetchGoldenNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">领取积分数</p>
          <p class="type-num">{{detailInfo.fetchGoldenCount}}</p>
        </div>
        <div class="item">
          <p class="type-name">偷取积分人数</p>
          <p class="type-num">{{detailInfo.stealGoldenNumber}}</p>
        </div>
        <div class="item">
          <p class="type-name">偷取积分数</p>
          <p class="type-num">{{detailInfo.stealGoldenCount}}</p>
        </div>
      </div>
    </div>

    <div class="section-wrap">
      <div class="title-wrap">
        <h1 class="title">用户列表</h1>
        <el-button class="title-btn" type="primary" @click="exportData">导出</el-button>
      </div>
      <el-table class="table-wrap"
        header-row-class-name="table-header"
       :data="tableDataList">
        <el-table-column
          prop="regTime"
          align="center"
          label="注册时间">
        </el-table-column>
        <el-table-column
          align="center"
          prop="unionId"
          label="UnionID">
        </el-table-column>
        <el-table-column
          align="center"
          prop="nickName"
          label="用户昵称">
        </el-table-column>
        <el-table-column
          align="center"
          label="领取积分数">
          <template slot-scope="scope">
            {{scope.row.fetchGoldenCount > 0 ? `+${scope.row.fetchGoldenCount}` : 0}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="偷取积分数">
          <template slot-scope="scope">
            {{scope.row.stealGoldenCount > 0 ? `+${scope.row.stealGoldenCount}` : 0}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="inviteCount"
          label="邀请次数">
        </el-table-column>
        <el-table-column
          align="center"
          prop="inviteNumber"
          label="成功邀请人数">
        </el-table-column>
        <el-table-column
          align="center"
          prop="inviteAward"
          label="分享积分奖励">
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button @click="showDetail(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="(totalCount / pageSize)<= 1"
        :page-size="pageSize"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getInviteSendPointsData, exportDataDefault } from '@/api/services/integralActiveManage'

export default {
  name: 'invite-send-points',
  components: {},
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      platformTypeValue: '', // 选择的平台类型，默认为空，表示全部
      platformList: {
        '1': '很实惠',
        '2': '聚驿优品'
      },
      isPost: false,
      detailInfo: {
        visitorNumber: 0, // 首页访问人数
        inviteNumber: 0, // 邀请人数
        inviteAward: 0, // 邀请金豆奖励
        fetchGoldenNumber: 0, // 领金豆人数
        fetchGoldenCount: 0, // 领金豆数
        stealGoldenNumber: 0, // 偷金豆人数
        stealGoldenCount: 0 // 偷金豆数
      },
      timeListValue: [],
      tableDataList: [],
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      page: 1
    }
  },
  created() {
    let _this = this

    _this.getListData()
  },
  methods: {
    getListData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getInviteSendPointsData(params).then(res => {
          _this.detailInfo = {
            visitorNumber: res.visitorNumber,
            inviteNumber: res.inviteNumber,
            inviteAward: res.inviteAward,
            fetchGoldenNumber: res.fetchGoldenNumber,
            fetchGoldenCount: res.fetchGoldenCount,
            stealGoldenNumber: res.stealGoldenNumber,
            stealGoldenCount: res.stealGoldenCount
          }

          _this.tableDataList = res.list
          _this.totalCount = res.totalCount
          _this.pageSize = Number(res.pageSize)
          _this.isPost = false
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          startAt: _this.timeListValue[0] || '',
          endAt: _this.timeListValue[1] || '',
          pageSize: _this.pageSize,
          page: _this.page,
          isExport: 1
        }

      exportDataDefault('inviteSendPoints', params)
    },

    platformSelect() {
      this.getListData()
    },

    handleSizeChange(size) {
      this.pageSize = size
    },
    handleCurrentChange(page) {
      let _this = this

      _this.page = page
      _this.getListData()
    },

    timeDataSelect(time) {
      let _this = this

      _this.timeListValue = time
      _this.getListData()
    },

    showDetail(item) {
      this.$router.push({
        name: 'inviteSendDetail',
        query: {
          nickName: item.nickName,
          unionId: item.unionId,
          appletId: this.platformTypeValue,
          platformType: this.platformList[this.platformTypeValue] || '全部'
        }
      })
    }
  }
}
</script>

<style lang="less">
</style>

<style lang="less" scoped>
.invite-send-points-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;
  }

  .ops-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #F2F3F7;
    margin-bottom: 16px;

    .title {
      flex: 1 0 90px;
      margin-right: 16px;
      margin-bottom: 12px;
      padding: 0;
      border: none;
    }
    .el-button {
      margin-bottom: 12px;
    }
  }

  .title-wrap {
    position: relative;
    border-bottom: 1px solid #F2F3F7;
    padding-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      padding-bottom: 0px;
      border-bottom: none;
    }
  }

  .title {
    padding-bottom: 12px;
    font-size: 20px;
    line-height: 28px;
    border-bottom: 1px solid #F2F3F7;
  }

  .seconde-title {
    margin-top: 16px;
    font-size: 16px;
    line-height: 20px;
  }

  .select-wrap {
    margin-bottom: 12px;
    width: 144px;
    margin-right: 16px;
  }

  .picker-wrap {
    margin-bottom: 12px;
    width: 300px;
    margin-right: 16px;
  }

  .digital-info-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .item {
      padding: 24px 16px 0px;
      display: flex;
      flex-direction: column;
      align-items: center;
      min-width: 150px;

      .type-name {
        font-size: 14px;
        color: #606166;
      }

      .type-num {
        font-size: 40px;
        line-height: 56px;
        color: #4D5AFF;
      }
    }
  }

  .table-wrap {
    margin-top: 24px;
  }
  .pagination-wrap {
    margin-top: 16px;
    text-align: right;
  }
}
</style>
