<template>
  <div>
    <div class="container">
      <Title-item type="search" title="商品数据" @handleData="search"></Title-item>
      <Total-data :totalList="productData"></Total-data>
    </div>
    <div class="container">
      <Title-item type="export" title="商品销售排行" @handleData="exportData"></Title-item>
      <el-table class="mgt24" :data="tableData" :header-cell-style="{ background:'#F2F3F7',color:'#606266'}">
        <el-table-column prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column prop="pv" label="PV"></el-table-column>
        <el-table-column prop="uv" label="UV"></el-table-column>
        <el-table-column prop="pointsAmount" label="消耗积分"></el-table-column>
        <el-table-column prop="tradeAmount" label="支付金额">
          <template slot-scope="scope">
            {{scope.row.tradeAmount | price}}
          </template>
        </el-table-column>
        <el-table-column prop="buyNumber" label="商品销售量"></el-table-column>
        <el-table-column label="商品转化率">
          <template slot-scope="scope">
            {{scope.row | conversion}}
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="20"
          layout="prev, pager, next"
          :page-count="totalPage"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import TotalData from '../components/TotalData'
import TitleItem from '../components/TitleItem'
import { getGoodsData, exportGoodsData } from '@/api/services/dataProfile'
import moment from 'moment'
import { toFixed } from '@/utils'

const DATE = new Date()
let beforeTime, DATE_STR
beforeTime = day => DATE - 86400 * day * 1000 // day 获取多少天之前的
DATE_STR = moment(beforeTime(1)).format('YYYYMMDD') // 昨天

export default {
  name: 'index',
  components: {
    TotalData,
    TitleItem
  },
  filters: {
    conversion(row) {
      if (!row.pv) return '0%'
      return toFixed((row.buyNumber / row.pv) * 100) + '%'
    },
    price(data) {
      return toFixed(data / 100)
    }
  },
  data() {
    return {
      params: {
        page: 0,
        startAt: DATE_STR,
        endAt: DATE_STR
      },
      tableData: [],
      totalPage: 0,
      productData: [
        {
          key: 'goodsDetailPagePv',
          label: '商品详情访问pv',
          value: 0
        },
        {
          label: '商品详情页访问uv',
          key: 'goodsDetailPageUv',
          value: 0
        },
        {
          label: '商品销售量',
          key: 'saleNumber',
          value: 0
        }
      ]
    }
  },
  methods: {
    search(val) {
      Object.assign(this.params, val)
      this.getDataList()
    },
    exportData() {
      exportGoodsData(this.params)
    },
    handleCurrentChange(val) {
      this.params.page = val - 1
      this.getDataList()
    },
    getDataList() {
      getGoodsData(this.params).then(res => {
        if (!res) return
        const { goodsData, saleGoodsData } = res
        this.productData.forEach(v => {
          v.value = goodsData[v.key]
        })
        this.tableData = saleGoodsData.list
        this.totalPage = saleGoodsData.totalPage
      })
    }
  },
  created() {
    this.getDataList()
  }
}
</script>

<style scoped lang="less">
  .container {
    background: #fff;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 24px 16px;
  }
</style>
