<template>
  <div>
    <div class="container">
      <Title-item class="mgb24 mgt8" title="总数据">
        <el-date-picker
          v-model="daterange"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
        <el-select v-model="userSourceValue" clearable placeholder="请选择端口" class="mgl16">
          <el-option
            v-for="item in userSource"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" style="margin-left: 16px;" @click="search">搜索</el-button>
      </Title-item>
      <Total-data :totalList="totalList"></Total-data>
    </div>
    <!--漏斗-->
    <div class="container">
      <Title-item class="mgb24 mgt8" title="核心漏斗"></Title-item>
      <div class="funne_container" style="justify-content: start">
        <Funnel :dataList="goodsFunnel" title="兑换商品数据" style="flex:1"></Funnel>
        <Funnel :dataList="couponFunnel" title="兑换劵数据" style="flex:1"></Funnel>
        <Funnel :dataList="friendFunnel" title="邀请好友数据" style="flex:1"></Funnel>
      </div>
    </div>

    <!--折线图-->
    <div class="container" v-for="item in lineList" :key="item.id">
      <Title-item class="mgb24 mgt8" :title="item.name"></Title-item>
      <div :id="item.id" style="width:100%; height: 400px;"></div>
    </div>
    <div class="container">
      <Title-item class="mgb24 mgt16" title="兑换排名"></Title-item>
      <div class="funne_container">
        <Conversion :TotalData="buyTotalData" title="访客-兑换商品转化率"
                    :dataList="goodsTableList"></Conversion>
        <Conversion class="mgl16" :TotalData="couponlData" title="访客-兑换劵转化率"
                    :dataList="couponlDataTableList"></Conversion>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import TitleItem from '../juyiProfile/components/TitleItem'
import TotalData from '../juyiProfile/components/TotalData'
import { hshDataOverview } from '@/api/services/dataProfile'
import Conversion from './components/Conversion'
import Funnel from './components/Funnel'
import moment from 'moment'
import { funnelColumns, totalColumns } from './components/columns'
// import data from './components/data.json'

export default {
  components: {
    Conversion,
    Funnel,
    TitleItem,
    TotalData
  },
  data() {
    return {
      buyTotalData: {},
      couponlData: {},
      friendFunnel: [],
      goodsFunnel: [],
      couponFunnel: [],
      RETURN_TYPE: 'HOUR',
      goodsTableList: [],
      lineList: [
        {
          id: 'main_visit',
          name: '访问分析'
        },
        {
          id: 'main_beans',
          name: '金豆数据分析'
        },
        {
          id: 'main_user',
          name: '用户积分分布'
        }
      ],
      couponlDataTableList: [],
      daterange: [
        new Date(),
        new Date()
      ],
      totalList: totalColumns(),
      userSourceValue: '',
      userSource: [
        {
          label: 'H5',
          value: '1'
        },
        {
          label: '小程序',
          value: '2'
        }
      ]
    }
  },
  methods: {
    // 漏斗数据排序
    funnel(data) {
      const compare = (obj1, obj2) => {
        const [val1, val2] = [Number(obj1.value), Number(obj2.value)]
        if (val1 < val2) {
          return 1
        } else if (val1 > val2) {
          return -1
        } else {
          return 0
        }
      }
      return data.sort(compare)
    },
    search() {
      let date = this.daterange
      this.getHshDataOverview(
        date,
        this.userSourceValue
      )
    },
    getHshDataOverview(date, pageType) {
      let startAt = date && date[0] ? moment(date[0]).format('YYYYMMDD') : '',
        endAt = date && date[1] ? moment(date[1]).format('YYYYMMDD') : '',
        RETURN_TYPE = startAt !== endAt ? 'DAY' : 'HOUR'
      const params = {
        startAt,
        endAt,
        returnType: RETURN_TYPE,
        pageType
      }
      this.RETURN_TYPE = RETURN_TYPE
      if (!params.pageType) delete params.pageType
      hshDataOverview(params).then(res => {
        if (!res) return
        // let res = data[0].return_msg
        const { totalData, scope, funnelData, overAnalyData, integralAnalyGrantData, integralAnalyRecycleData, integralDistribution } = res
        let { goodsFunnel, couponFunnel, friendFunnel } = funnelColumns(funnelData, totalData[this.totalList[0].key])
        this.totalList.forEach(v => {
          v.value = totalData[v.key]
        })
        /**
         * 漏斗
         * goodsFunnel 兑换商品数据
         * couponFunnel 兑换劵数据
         * friendFunnel 邀请好友数据
         * */
        this.goodsFunnel = this.funnel(goodsFunnel)
        this.couponFunnel = this.funnel(couponFunnel)
        this.friendFunnel = this.funnel(friendFunnel)
        /**
         * 访问分析 折线图
         */
        this.accessAnalyze(overAnalyData, RETURN_TYPE)
        /**
         * 金豆数据分析 折线图
         * */
        this.beansAnalyze(integralAnalyGrantData, integralAnalyRecycleData, RETURN_TYPE)
        /**
         * 用户积分分布 圆柱图
         */
        this.userIntegral(scope, integralDistribution)
        /**
         * 底部数据统计-兑换排名
         * */
        this.footerData(res)
      })
    },
    // 金豆数据分析
    beansAnalyze(integralAnalyGrantData, integralAnalyRecycleData, RETURN_TYPE) {
      let [beans, grantData, recycleData, xAxis] = [
        stack(),
        this.getIntegralData(integralAnalyGrantData, RETURN_TYPE), // 发金豆
        this.getIntegralData(integralAnalyRecycleData, RETURN_TYPE),
        RETURN_TYPE === 'HOUR' ? this.getHours() : this.getDay()
      ]
      const [grantDatalNums, recycleDatalNums] = [this.initData(grantData, xAxis), this.initData(recycleData, xAxis)]
      beans.xAxis[0].data = xAxis
      beans.series = this.series({ name: '发金豆数', data: grantDatalNums }, {
        name: '回收金豆数',
        data: recycleDatalNums
      })
      beans.legend.data = ['发金豆数', '回收金豆数']
      echarts.init(document.getElementById('main_beans')).setOption(beans)
    },
    // 访问分析
    accessAnalyze(overAnalyData, RETURN_TYPE) {
      let [xAxis, visit, overAnalyUserData, integralNums] = [
        RETURN_TYPE === 'HOUR' ? this.getHours() : this.getDay(),
        stack(),
        this.getLineChartData(overAnalyData, 1, RETURN_TYPE),
        null
      ]
      visit.xAxis[0].data = xAxis
      // 访问人数, 积分用户数
      const [visitUserNums, integralData] = [this.initData(overAnalyUserData, xAxis), this.getLineChartData(overAnalyData, 2, RETURN_TYPE)]
      integralNums = this.initData(integralData, xAxis)
      visit.series = this.series({ name: '访问人数', data: visitUserNums }, {
        name: '积分用户数',
        data: integralNums
      })
      visit.legend.data = ['访问人数', '积分用户数']
      echarts.init(document.getElementById('main_visit')).setOption(visit)
    },
    userIntegral(scope, integralDistribution) {
      let suerIntegral = columnar(), xScope, maxNumber, serIntegralDate
      xScope = Object.keys(scope).map(k => `${scope[k].min}-${scope[k].max || '∞'}`)
      maxNumber = Math.max(...integralDistribution.map(v => v._id - 0))
      serIntegralDate = this.initArray(maxNumber)
      integralDistribution.forEach(v => {
        serIntegralDate[v._id - 1] = v.userNumber
      })
      suerIntegral.xAxis[0].data = xScope
      suerIntegral.series[0].data = serIntegralDate
      echarts.init(document.getElementById('main_user')).setOption(suerIntegral)
    },
    footerData({ buyTotalData, entityData, couponsData }) {
      const [goodsTotal, couponlData] = [
        buyTotalData.filter(v => v._id === 1)[0],
        buyTotalData.filter(v => v._id === 3)[0]
      ]
      this.goodsTableList = entityData
      this.buyTotalData = goodsTotal || {}
      this.couponlData = couponlData || {}
      this.couponlDataTableList = couponsData
    },
    mapData(v, key, RETURN_TYPE) {
      let time = (v.time + '000') - 0
      const HOUR = moment(time).format('HH:mm')
      return {
        val: v[key],
        date: v.time,
        // time: RETURN_TYPE === 'HOUR' ? HOUR : new Date(time).getDate()
        time: RETURN_TYPE === 'HOUR' ? HOUR : moment(time).format('YYYY-MM-DD')
      }
    },
    initArray(number) {
      let arr = []
      for (let i = 0; i < number; i++) {
        arr.push(0)
      }
      return arr
    },
    /* 初始化折线图数据 */
    initData(data = [], time) {
      if (!data.length) return []
      const number = this.getArrLength(data, time)
      let arr = this.initArray(number)
      data.forEach(v => {
        let index = time.indexOf(v.time)
        arr[index] = v.val
      })
      return arr
    },
    getLineChartData(arr = [], type, RETURN_TYPE = 'DAY') {
      return arr.filter(v => v.type === type).map(v => this.mapData(v, 'docCount', RETURN_TYPE))
    },
    getIntegralData(arr = [], RETURN_TYPE = 'DAY') {
      return arr.length ? arr.map(v => this.mapData(v, 'goldenSum', RETURN_TYPE)) : []
    },
    getArrLength(arr, times) {
      /*
      * 如果RETURN_TYPE是 DAY 说明是按天统计 则找出日期差
      * 如果RETURN_TYPE不是 DAY 说明是按时间统计 则找出最大时间
      * */
      if (this.RETURN_TYPE === 'DAY') return this.GET_DAY().DAY + 1
      const date = String(Math.max(...arr.map(v => Number(v.date))))
      let result = arr.filter(v => v.date === date)[0]
      return times.indexOf(result.time)
    },
    series() {
      const arg = Array.from(arguments)
      return arg.map(v => ({
        name: v.name,
        type: 'line',
        smooth: true,
        data: v.data
      }))
    },
    getHours() {
      let arr = []
      for (let i = 0; i < 24; i++) {
        let date = new Date('2019/01/01 00:00:00').getTime() + i * 60 * 60 * 1000
        arr.push(moment(date).format('HH:mm'))
      }
      return arr
    },
    GET_DAY() {
      let [start, end, oneDay] = [this.daterange[0] - 0, this.daterange[1] - 0, 86400 * 1000]
      const DAY = (end - start) / oneDay
      return { DAY, start, end, oneDay }
    },
    getDay() {
      if (Array.isArray(this.daterange) && this.daterange.length >= 2) {
        let [arr, { DAY, start, oneDay }] = [[], this.GET_DAY()]
        for (let i = 0; i <= DAY; i++) {
          arr.push(moment(start + oneDay * i).format('YYYY-MM-DD'))
        }
        return arr
      }
    }
  },
  mounted() {
    this.getHshDataOverview(this.daterange)
  }
}

function stack() {
  return {
    tooltip: {
      trigger: 'axis'
    },
    dataZoom: [
      {
        show: true,
        realtime: true,
        start: 0,
        end: 100
      }
    ],
    legend: {
      data: []
    },
    xAxis: [
      {
        type: 'category',
        boundaryGap: false,
        axisLine: { onZero: false },
        data: []
      }
    ],
    yAxis: {
      type: 'value'
    },
    color: ['#6699FF', '#836DF2', '#CE61F2']
  }
}

/**
 * 柱状图配置
 */
function columnar() {
  return {
    color: ['#6699FF', '#836DF2'],
    tooltip: {
      trigger: 'axis',
      axisPointer: { // 坐标轴指示器，坐标轴触发有效
        type: 'shadow'// 默认为直线，可选为：'line' | 'shadow'
      }
    },
    grid: {
      containLabel: true
    },
    xAxis: [
      {
        type: 'category',
        data: [],
        axisTick: {
          alignWithLabel: true
        }
      }
    ],
    yAxis: [{
      type: 'value'
    }],
    series: [{
      name: '分布用户数',
      data: [],
      barWidth: '50%',
      type: 'bar'
    }]
  }
}
</script>

<style scoped lang="less">
  .container {
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 16px;
    background: #fff;
  }

  .funne_container {
    display: flex;
  }

  .mgb24 {
    margin-bottom: 24px;
  }

  .mgt16 {
    margin-top: 16px;
  }

  .mgt8 {
    margin-top: 8px;
  }
</style>
