import request from '../request'

// 新增用户数据
export function newUsersApi(data) {
  return request({
    url: '/wallet/users-data/new-users',
    method: 'get',
    params: data
  })
}
// 首次互换用户数据
export function firstExchange(data) {
  return request({
    url: '/wallet/users-data/first-exchange',
    method: 'get',
    params: data
  })
}
