<template>
  <div class="good-list" v-loading="loading">
    <h2 class="title">优惠券列表</h2>

    <div class="query">
      <div class="query-list">
        <el-select
          class="query-list-item"
          v-model="query.appletId"
          placeholder="请选择商家类型">
          <el-option
            v-for="(item, key) in appletList"
            :key="item"
            :label="item"
            :value="key">
          </el-option>
        </el-select>

        <el-input v-model="query.name" :clearable="true" class="query-list-item keyword" placeholder="输入优惠券名称"></el-input>
        <el-input v-model="query.couponId" :clearable="true" class="query-list-item keyword" placeholder="输入优惠券ID"></el-input>

        <el-select
          class="query-list-item"
          v-model="query.isPutaway"
          :clearable="true"
          placeholder="请选择状态">
          <el-option
            v-for="(item, key) in statusList"
            :key="item"
            :label="item"
            :value="key">
          </el-option>
        </el-select>

        <el-date-picker
          class="query-list-item"
          v-model="query.time"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions">
        </el-date-picker>

        <el-button class="query-list-item" type="primary" @click="handleScreen">搜索</el-button>
        <el-button class="query-list-item" type="primary" @click="exportData">导出</el-button>
      </div>
    </div>

    <el-table :data="tableData">
      <el-table-column label="优惠券ID" prop="couponId"></el-table-column>
      <el-table-column label="创建时间" prop="formatCreatedDate" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="优惠券名称" prop="name" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="logo">
        <template slot-scope="scope">
          <img :src="scope.row.logo" style="width: 50px;" alt="" srcset="">
        </template>
      </el-table-column>
      <el-table-column label="平台" prop="appletIdName"></el-table-column>
      <el-table-column label="发放数量" prop="totalNum"></el-table-column>
      <el-table-column label="剩余数量" prop="surplusNum"></el-table-column>
      <el-table-column label="领取数量" prop="drawNum"></el-table-column>
      <el-table-column label="曝光次数" prop="exposureNum"></el-table-column>
      <el-table-column label="状态" prop="stat"></el-table-column>
      <el-table-column label="优惠券到期时间" prop="endTime"></el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :hide-on-single-page="true"
        :current-page.sync="page.currentPage"
        :page-size.sync="page.pageSize"
        layout="prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getHshCouponList, exportHshCouponList } from '../../../api/services/pointsMallManagement'
import dateOptionsMixins from '@/components/dateOptionsMixins'
import config from '@/config'
import { formatTime } from '@/utils'

export default {
  mixins: [dateOptionsMixins],
  data() {
    return {
      loading: false, // 加载loading
      query: {
        appletId: '1', // 商家类型
        isPutaway: '', // 状态
        couponId: '', // 优惠券ID
        name: '', // 优惠券名称
        time: '' // 时间筛选
      },
      statusList: {
        0: '下架',
        1: '上架'
      },
      page: {
        total: 0, // 总条数
        currentPage: 1,
        pageSize: 20
      },
      // 商家类型列表、
      appletList: config.appletList,
      tableData: [] // 表格数据
    }
  },
  created() {
    this.getDataList()
  },
  methods: {
    // 获取表格数据
    getDataList() {
      let _this = this

      _this.loading = true

      getHshCouponList(_this.getQueryData()).then(res => {
        let page = _this.page,
          appletList = _this.appletList

        res.data.forEach(couponItem => {
          couponItem.surplusNum = Number(couponItem.totalNum) - Number(couponItem.drawNum)
          couponItem.appletIdName = appletList[couponItem.appletId]
          couponItem.formatCreatedDate = formatTime(Number(couponItem.createdAt) * 1000, 'yyyy-MM-dd hh:mm')
        })

        _this.tableData = res.data
        page.total = Number(res.total)

        _this.loading = false
      }).catch(() => {
        _this.loading = false
      })
    },

    // 处理类型筛选
    handerQueryType(status) {
      this.query.onSale = status
      this.handleScreen()
    },

    // 处理每页条数改变
    handleScreen(pageSize) {
      let _this = this

      _this.page.currentPage = 1
      _this.getDataList()
    },

    /**
     * 获取筛选数据
     * @param { Boolean } type 类型  screen 筛选 export 导出
     */
    getQueryData(type = 'screen') {
      let _this = this,
        page = _this.page,
        query = _this.query,
        couponId = query.couponId,
        name = query.name,
        isPutaway = query.isPutaway,
        time = query.time,
        params = {
          appletId: query.appletId, // 商家类型
          page: page.currentPage
        }

      if (couponId) {
        params.couponId = couponId
      }

      if (name) {
        params.name = name
      }

      if (isPutaway !== '') {
        params.isPutaway = isPutaway
      }

      if (time) {
        params.startTime = parseInt(time[0].getTime() / 1000)
        params.endTime = parseInt(time[1].getTime() / 1000)
      }

      // if (type === 'export') {
      //   params.isExport = 1
      // }

      return params
    },

    // 导出
    exportData() {
      exportHshCouponList(this.getQueryData('export'))
    },

    // 处理分页
    handleCurrentChange() {
      this.getDataList()
    }
  }
}
</script>

<style lang="less" scoped>
.good-list {
  padding: 0 16px;
  border-radius: 8px;
  background: #fff;

  & > .title {
    padding: 24px 0;
    font-size: 20px;
    line-height: 28px;
    color: #333;
  }

  .query {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #333;
    padding-bottom: 16px;

    .query-type {
      display: flex;
      align-items: center;

      .query-type-item {
        position: relative;
        padding: 16px 0;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 0;
        cursor: pointer;

        & + .query-type-item {
          margin-left: 40px;
        }

        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 2px;
          background: #4D5AFF;
        }

        &.active {
          color: #4D5AFF;

          &::after {
            display: block;
          }
        }
      }
    }

    .query-list {
      display: flex;
      align-items: center;

      .query-list-item {
        & + .query-list-item {
          margin-left: 16px;
        }

        &.keyword {
          width: 164px;
        }
      }
    }
  }

  .pagination {
    padding-bottom: 16px;
    text-align: right;
  }
}
</style>
