<template>
  <div class="search_container">
    <span :class="[activeTime === index&&'active_time', 'mgl16']" v-for="(item, index) in searchTime" :key="index"
          @click="selectorTime(index)">{{item}}</span>
    <el-date-picker
      class="mgl16"
      v-model="time"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
    <el-button style="margin-left: 16px" type="primary" @click="search">搜 索</el-button>
  </div>
</template>

<script>
import moment from 'moment'

const DATE = new Date()
let beforeTime, DATE_STR
beforeTime = day => DATE - 86400 * day * 1000 // day 获取多少天之前的
DATE_STR = moment(beforeTime(1)).format('YYYYMMDD') // 昨天
export default {
  name: 'SearchTime',
  data() {
    return {
      time: '',
      params: {
        startAt: DATE_STR,
        endAt: DATE_STR
      },
      activeTime: 0,
      searchTime: [
        '昨日',
        '最近7天',
        '最近30天'
      ]
    }
  },
  methods: {
    moment(time) {
      return {
        startAt: moment(time[0]).format('YYYYMMDD'),
        endAt: moment(time[1]).format('YYYYMMDD')
      }
    },
    search() {
      let params = Array.isArray(this.time) && this.time.length >= 2 ? this.moment(this.time) : this.params
      this.$emit('search', params)
    },
    selectorTime(index) {
      // 记录index 设置选中状态样式
      this.activeTime = index
      // 清空时间选择器
      this.time = []
      // 昨天、最近7天、最近30天
      const day = [1, 7, 30]
      /*
      * before 之前多少天
      * current 当天
      * */
      let [before, current] = [beforeTime(day[index]), DATE]
      this.params = index === 0 ? this.moment([before, before]) : this.moment([before, current])
      this.$emit('search', this.params)
    }
  }
}
</script>

<style lang='less' scoped>
  .search_container {
    span {
      cursor: pointer;
      color: #000;
    }

    .active_time {
      color: #4D5AFF;
      font-weight: bold;
    }
  }

  .mgl16 {
    margin-left: 16px;
  }
</style>
