import service from '../../api/request'
import urls from '../../config/urls'

// url拼接参数
const parseParams = (uri, params) => {
    const paramsArray = []
    Object.keys(params).forEach(
      key => params[key] && paramsArray.push(`${key}=${params[key]}`)
    )
    if (uri.search(/\?/) === -1) {
      uri += `?${paramsArray.join('&')}`
    } else {
      uri += `&${paramsArray.join('&')}`
    }
    return uri
  },
  // 积分兑换
  postPoints = param => {
    return service.post(
      'property/points/query-statistics-points-action-record',
      param
    )
  },
  // 积分兑换导出
  downloadPost = param => {
    let url =
      urls.backend +
      '/property/points/query-statistics-points-action-record?isExport=1'
    param.t = sessionStorage.getItem('t')

    url = parseParams(url, param)
    console.log(url)
    location.href = url
  },
  downloadBuy = param => {
    let url = urls.backend + '/property/points/export-cash-buy-day-record'
    param.t = sessionStorage.getItem('t')
    url = parseParams(url, param)
    location.href = url
  },
  downloadGet = param => {
    let url = urls.backend + '/property/points/export-points-receive-day'
    param.t = sessionStorage.getItem('t')
    url = parseParams(url, param)
    location.href = url
  },
  downloadSettle = param => {
    let url = urls.backend + '/property/points/export-points-settle'
    param.t = sessionStorage.getItem('t')
    url = parseParams(url, param)
    location.href = url
  },
  downloadAssets = param => {
    let url = urls.backend + '/property/points/export-points-total-value'
    param.t = sessionStorage.getItem('t')
    url = parseParams(url, param)
    location.href = url
  },
  // 积分获取
  getPoints = param => {
    const url = parseParams('property/points/get-points-receive-day', param)
    return service.get(url)
  },
  getBuy = param => {
    const url = parseParams('property/points/get-cash-buy-day-record', param)
    return service.get(url)
  },
  // 积分结算
  getPointsSettle = param => {
    param.appletId = param.appletId || 1
    const url = parseParams('property/points/get-points-settle', param)
    return service.get(url)
  },
  // 积分资产
  getPointsTotalValue = param => {
    param.appletId = param.appletId || 1
    const url = parseParams('property/points/get-points-total-value', param)
    return service.get(url)
  }

export {
  downloadGet,
  downloadBuy,
  downloadPost,
  downloadSettle,
  downloadAssets,
  getPoints,
  postPoints,
  getBuy,
  getPointsSettle,
  getPointsTotalValue
}
