<script>
export default {
  props: {
    data: {
      type: Array,
      default() {
        return []
      }
    }
  },
  render() {
    return (
      <div class="card-wrp">
        {this.data.map(item => (
          <div class="card" key={item.label}>
            <h2>{item.label}</h2>
            <p>{item.value}</p>
          </div>
        ))}
      </div>
    )
  }
}
</script>
<style lang="less" scoped>
.card-wrp {
  width: 100%;
  display: flex;
  flex-flow: wrap;
}
h2 {
  font-weight: 700;
  font-size: 24px;
}
.card {
  background: #f1f1f1;
  margin: 36px 24px 36px 0;
  height: 110px;
  min-width: 300px;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
h2 {
  font-size: 14px;
  color: #606166;
}
p {
  padding-top: 12px;
  font-size: 40px;
  line-height: 40px;
  color: #4d5aff;
}
</style>
