
// 平台用户管理分路由
import integralActiveManage from './index'
import inviteSendPoints from './components/inviteSendPoints'
import turnCardExchangeData from './components/turnCardExchangeData'
import inviteSendDetail from './components/inviteDetail'
import exchangeDataDetail from './components/exchangeDetail'

export default {
  path: '/integral-active-manage',
  name: 'integralActiveManage',
  component: integralActiveManage,
  children: [
    {
      path: 'invite-send-points',
      name: 'inviteSendPoints',
      component: inviteSendPoints
    },
    {
      path: 'turncard-exchange-data',
      name: 'turnCardExchangeData',
      component: turnCardExchangeData
    },
    {
      path: 'invite-send-detail',
      name: 'inviteSendDetail',
      component: inviteSendDetail
    },
    {
      path: 'exchange-data-detail',
      name: 'exchangeDataDetail',
      component: exchangeDataDetail
    }
  ]
}
