<script>
export default {
  props: {
    formConfig: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    //   初始化表单绑定的属性

    const obj = {}
    this.formConfig.map(item => {
      obj[item.key] = item.value
    })

    return obj
  },
  render() {
    //   使用策略模式循环渲染出每一个表单项
    return (
      <div>
        {this.formConfig.map(item => this[item.type](item))}
        <el-button type="primary" onclick={this.submit}>
          查询
        </el-button>
      </div>
    )
  },
  methods: {
    // 提交表单派发submit事件给父组件
    submit() {
      const obj = {},
        arr = this.formConfig.map(item => item.key)
      arr.forEach(item => {
        obj[item] = this[item]
      })
      this.$emit('submit', obj)
    },

    input(item) {
      return (
        <el-input
          placeholder={item.placeholder}
          class="input"
          v-model={this[item.key]}></el-input>
      )
    },
    datePicker(item) {
      return (
        <el-date-picker
          v-model={this[item.key]}
          class="picker"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"></el-date-picker>
      )
    },
    select(item) {
      return (
        <el-select
          placeholder={item.placeholder || '请选择平台'}
          v-model={this[item.key]}>
          {item.options.map(item => (
            <el-option
              key={item.value}
              label={item.label}
              value={item.value}></el-option>
          ))}
        </el-select>
      )
    }
  }
}
</script>
<style lang="less" scoped>
.input {
  width: 250px;
}
</style>
