<template>
  <div class="digit-detail-info-wrap">
    <div class="section-wrap">
      <h1 class="title">用户积分明细</h1>

      <div class="ops-wrap">
        <el-select @change="platformSelect"
          v-model="platformTypeValue"
          class="select-wrap"
          placeholder="请选择平台">
          <el-option value="" label="全部平台"></el-option>
          <el-option
            v-for="(item, index) in platformList"
            :key="item"
            :value="index"
            :label="item">
          </el-option>
        </el-select>
        <span class="username">用户昵称: {{detailInfo.username}}</span>
        <el-button class="ops-btn" type="primary" @click="exportData">导出</el-button>
      </div>

      <el-table class="table-wrap"
        header-row-class-name="table-header"
        :data="tableDataList">
        <el-table-column
          align="center"
          prop="employTime"
          label="日期">
        </el-table-column>
        <el-table-column
          align="center"
          prop="sourceName"
          label="类型">
        </el-table-column>
        <el-table-column
          align="center"
          prop="pointValue"
          label="积分">
          <template slot-scope="scope">
            {{scope.row.addOrReduce == 1 ? '+' : '-'}}{{scope.row.pointValue}}
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="pagination-wrap"
        layout="sizes, total, prev, pager, next, jumper"
        :hide-on-single-page="(totalCount / pageSize)<= 1"
        :page-size="pageSize"
        :total="totalCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getActiveDetailData, exportDataDefault } from '@/api/services/platformUserManage'

export default {
  name: 'platform-user-digital-detail',
  components: {},
  data() {
    return {
      tableDataList: [],
      detailInfo: {
        unionId: '',
        username: ''
      },
      platformTypeValue: '', // 选择的平台类型，默认为空，表示全部
      platformList: {
        '1': '很实惠',
        '2': '聚驿优品'
      },
      totalCount: 0, // 总数据条目
      pageSize: 20, // 一页数据量
      totalPage: 1, // 总页数
      page: 0
    }
  },
  created() {
    let _this = this

    _this.detailInfo = _this.$route.query
    _this.getListData()
  },
  methods: {
    getListData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          unionId: _this.detailInfo.unionId,
          page: _this.page,
          pageSize: _this.pageSize
        }

      if (_this.isPost) {
        return false
      }

      _this.isPost = true
      return new Promise((resolve, reject) => {
        getActiveDetailData(params).then(res => {
          _this.tableDataList = res.list
          _this.totalCount = res.userList.totalCount
          _this.pageSize = Number(res.userList.pageSize)
          _this.isPost = false
          resolve(res)
        }).catch(msg => {
          _this.isPost = false
        })
      })
    },

    exportData() {
      let _this = this,
        params = {
          appletId: _this.platformTypeValue,
          unionId: _this.detailInfo.unionId,
          page: _this.page,
          pageSize: _this.pageSize,
          isExport: 1
        }

      exportDataDefault('platformUserDataDetail', params)
    },

    platformSelect() {
      this.getListData()
    },

    handleSizeChange(size) {
      this.pageSize = size
    },

    handleCurrentChange(page) {
      let _this = this

      _this.page = page - 1
      _this.getListData()
    },

    timeDataSelect(time) {
      let _this = this

      _this.timeListValue = time
      _this.getListData()
    }
  }
}
</script>

<style lang="less" scoped>
.digit-detail-info-wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .section-wrap {
    margin-bottom: 16px;
    padding: 24px 16px;
    background: #fff;
    color: #333;
    border-radius: 8px;

    .title {
      padding-bottom: 12px;
      font-size: 20px;
      line-height: 28px;
      border-bottom: 1px solid #F2F3F7;
    }

    .select-wrap {
      width: 144px;
      margin-right: 16px;
    }

    .ops-wrap {
      margin-top: 12px;
      vertical-align: text-bottom;
    }

    .username {
      vertical-align: text-bottom;
    }

    .ops-btn {
      float: right;
    }

    .table-wrap {
      margin-top: 24px;
    }

    .pagination-wrap {
      margin-top: 16px;
      text-align: right;
    }
  }
}
</style>
