<template>
  <div style="margin-bottom: 50px;">
    <el-tabs v-model="activeName" class="dataUotline_tabs" slot="header">
      <el-tab-pane label="数据指标" name="0"></el-tab-pane>
      <el-tab-pane label="订单数据" name="1"></el-tab-pane>
      <el-tab-pane label="商品数据" name="2"></el-tab-pane>
      <el-tab-pane label="留存数据" name="3"></el-tab-pane>
      <el-tab-pane label="用户数据" name="4"></el-tab-pane>
    </el-tabs>
    <component :is="componentName"></component>
  </div>
</template>

<script>
import DataIndicator from './dataIndicator'
import OrderData from './orderData'
import ProductData from './productData'
import RetainedData from './retainedData'
import UserData from './userData'

export default {
  components: {
    DataIndicator,
    OrderData,
    RetainedData,
    UserData,
    ProductData
  },
  data() {
    return {
      activeName: '0'
    }
  },
  computed: {
    componentName() {
      const component = ['DataIndicator', 'OrderData', 'ProductData', 'RetainedData', 'UserData']
      return component[this.activeName]
    }
  }
}
</script>

<style scoped lang="less">
  .el-tabs {
    padding-left: 16px;
    margin-top: 0;
    background: #fff;
  }

  .el-tabs__nav-wrap::after {
    background-color: transparent;
  }
</style>
<style lang="less">
  .dataUotline_tabs {
    /*margin-left: 12px;*/
    margin-bottom: 16px;

    > .el-tabs__header {
      margin-bottom: 1px;
    }

    > .el-tabs__header {
      > .el-tabs__nav-wrap {
        > .el-tabs__nav-scroll {
          > .el-tabs__nav {
            > .el-tabs__item {
              height: 50px;
              line-height: 50px;
            }

            > .el-tabs__item.is-active {
              color: #4D5AFF;
            }

            > .el-tabs__active-bar {
              background: #4D5AFF;
              height: 3px;
            }
          }
        }
      }

      > .el-tabs__nav-wrap::after {
        background-color: transparent;
      }
    }
  }

  .pagination {
    margin: 20px 0;
    text-align: right;
  }

  .mgt24 {
    margin-top: 24px;
  }

  .mgb24 {
    margin-bottom: 24px;
  }

  .mgl16 {
    margin-left: 16px;
  }
</style>
