import goodList from './goodList'
import pddGoodList from './pddGoodList'
import couponList from './couponList'
import pointsMallManagement from './index.vue'

export default {
  path: '/points-mall',
  component: pointsMallManagement,
  children: [
    {
      path: 'points-mall-good-list',
      name: 'pointsMallGoodList',
      component: goodList
    },
    {
      path: 'points-mall-pdd-good-list',
      name: 'pointsMallPddGoodList',
      component: pddGoodList
    },
    {
      path: 'points-mall-coupon-list',
      name: 'pointsMallCouponList',
      component: couponList
    }
  ]
}
