import aiduifen from './aiDuiFen.vue'
import userData from './userData.vue'
import activeData from './activeData.vue'
import exchangeDetail from './exchangeDetail.vue'

export default {
  path: '/aiduifen',
  name: 'aiduifen',
  redirect: '/aiduifen/addData',
  component: aiduifen,
  children: [
    {
      path: 'userData',
      name: 'aiduifenUserData',
      component: userData
    },
    {
      path: 'activeData',
      name: 'aiduifenActiveData',
      component: activeData
    },
    {
      path: 'exchangeDetail',
      name: 'aiduifenExchangeDetail',
      component: exchangeDetail
    }
  ]
}
