import IntegralAsset from './components/IntegralAsset'
import IntegralBuy from './components/IntegralBuy'

import IntegralManage from './components/IntegralManage'

// 资产用户管理分路由
import AssetsManage from './index'

export default {
  path: '/assets-manage',
  name: 'AssetsManage',
  component: AssetsManage,
  children: [
    {
      name: 'IntegralAsset',
      path: 'integral-asset',
      component: IntegralAsset
    },
    {
      name: 'IntegralBuy',
      path: 'integral-buy',
      component: IntegralBuy
    },
    {
      name: 'IntegralManage',
      path: 'integral-manage',
      component: IntegralManage
    }
  ]
}
