<template>
  <div class="good-list" v-loading="loading">
    <h2 class="title">商品列表</h2>

    <div class="query">
      <div class="query-type">
        <div
          class="query-type-item"
          v-for="item in qeuryStatusList"
          :class="{'active': query.status === item.value}"
          :key="item.label"
          @click="handerQueryType(item.value)">
          {{item.label}}
        </div>
      </div>

      <div class="query-list">
        <el-select
          class="query-list-item"
          v-model="query.appletId"
          placeholder="请选择商家类型"
          @change="handleScreenChange">
          <el-option
            v-for="(item, key) in appletList"
            :key="item"
            :label="item"
            :value="key">
          </el-option>
        </el-select>

        <el-input v-model="query.goodsName" class="query-list-item keyword" placeholder="输入商品名称"></el-input>
        <el-button class="query-list-item" type="primary" @click="handleScreenChange">搜索</el-button>
      </div>
    </div>

    <el-table :data="tableData" @sort-change="handlerSort">
      <el-table-column label="商品ID" prop="goodsId"></el-table-column>
      <el-table-column label="商品名称" prop="goodsName"></el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          {{scope.row.status == 1 ? '在售' : '仓库'}}
        </template>
      </el-table-column>
      <el-table-column label="原价" prop="originalPrice"></el-table-column>
      <el-table-column label="售价" prop="sellingPrice"></el-table-column>
      <el-table-column label="购买积分" prop="recyclingIntegral"></el-table-column>
      <el-table-column label="访问人数" prop="pv" sortable></el-table-column>
      <el-table-column label="商品销量" prop="salesVolume" sortable></el-table-column>
      <el-table-column label="商品库存" prop="stock"></el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        @size-change="handleScreenChange"
        @current-change="handleCurrentChange"
        :hide-on-single-page="true"
        :current-page.sync="page.currentPage"
        :page-size.sync="page.pageSize"
        :page-sizes="[10, 20, 50, 100]"
        layout="sizes, prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getGoodListApi } from '../../../api/services/pointsMallManagement'
import config from '@/config'

export default {
  data() {
    return {
      loading: false, // 加载loading
      query: {
        status: '', // 商品状态
        appletId: '2', // 商家类型
        goodsName: '', // 关键词筛选
        // 排序
        /**
         * {
            key: '', 排序字段值
            sort: '' 排序方向
          }
         */
        sortRules: null
      },
      page: {
        total: 0, // 总条数
        currentPage: 1,
        pageSize: 20
      },
      // 商品状态列表
      qeuryStatusList: [
        {
          label: '全部商品',
          value: ''
        },
        {
          label: '在售商品',
          value: 1
        },
        {
          label: '仓库中',
          value: 0
        },
        {
          label: '已售完',
          value: 2
        }
      ],
      // 商家类型列表、
      appletList: config.appletList,
      tableData: [] // 表格数据
    }
  },
  created() {
    this.getDataList()
  },
  methods: {
    // 获取表格数据
    getDataList() {
      let _this = this,
        page = _this.page,
        params = {
          ..._this.query,
          page: page.currentPage,
          pageSize: page.pageSize
        }

      _this.loading = true

      getGoodListApi(params).then(res => {
        let page = _this.page

        _this.tableData = res.list
        page.pageSize = Number(res.pageSize)
        page.total = Number(res.totalQuantity)

        _this.loading = false
      }).catch(() => {
        _this.loading = false
      })
    },

    // 处理类型筛选
    handerQueryType(status) {
      this.query.status = status
      this.handleScreenChange()
    },

    // 处理筛选改变
    handleScreenChange(pageSize) {
      let _this = this

      _this.page.currentPage = 1
      _this.getDataList()
    },

    // 处理分页
    handleCurrentChange() {
      this.getDataList()
    },

    // 处理排序
    handlerSort(sort) {
      if (sort.order) {
        this.query.sortRules = {
          key: sort.prop,
          sort: sort.order === 'ascending' ? 'ASC' : 'DESC'
        }
      } else {
        this.query.sortRules = null
      }

      this.getDataList()
    }
  }
}
</script>

<style lang="less" scoped>
.good-list {
  padding: 0 16px;
  border-radius: 8px;
  background: #fff;

  & > .title {
    padding: 24px 0;
    font-size: 20px;
    line-height: 28px;
    color: #333;
  }

  .query {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #333;
    padding-bottom: 16px;

    .query-type {
      display: flex;
      align-items: center;

      .query-type-item {
        position: relative;
        padding: 16px 0;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 0;
        cursor: pointer;

        & + .query-type-item {
          margin-left: 40px;
        }

        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 2px;
          background: #4D5AFF;
        }

        &.active {
          color: #4D5AFF;

          &::after {
            display: block;
          }
        }
      }
    }

    .query-list {
      display: flex;
      align-items: center;

      .query-list-item {
        & + .query-list-item {
          margin-left: 16px;
        }

        &.keyword {
          width: 164px;
        }
      }
    }
  }

  .pagination {
    padding-bottom: 16px;
    text-align: right;
  }
}
</style>
